&__carouselIndicator {
  $root: &;

  & &--label {
    width: 6px;
    height: 6px;
    display: block;
    position: relative;
    color: rgba($font-white, 0);
    background-color: rgba($bg-light-gray, 1);
    border-radius: 50%; }

  & &--selector {
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    left: 0;
    border: none; }

  & &--caption {
    font-size: $font-x-small;
    margin-top: 8px; }

  &.is-active {
    #{$root}--label {
      background-color: rgba($bg-black, 1); } }

  &.is-thumbnail {
    #{$root}--label {
      width: auto;
      height: auto;
      border: solid 1px rgba($border-white, 0);
      background-color: transparent;
      border-radius: 0; }

    &.is-active {
      #{$root}--label {
        border: solid 1px $border-black;
        background-color: transparent; } } } }
