&__tag {
  $root: &;

  &.is-footer {
    text-align: center;

    #{$root}--item {
      color: $font-white;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } } }

  &.is-drawerNavigation {
    #{$root}--item {
      color: rgba($font-white, 1);
      text-decoration: none;
      transition: color 0.5s ease;
      white-space: nowrap;

      &:hover {
        text-decoration: underline; } } } }
