@mixin clearfix {
  &::after {
    content: '';
    display: block;
    clear: both; } }

//media query
@mixin breakpoint($max: true, $max-media: phablet, $min: false, $min-media: phablet) {
  $media-width: (sp: $global-width-sp, phablet: $global-width-phablet, tablet: $global-width-tablet, pc: $global-width-pc);
  @if $max {
    @if $min {
      @media screen and (min-width: #{map-get($media-width, $min-media) + 1}px) and (max-width: #{map-get($media-width, $max-media)}px) {
        @content; } }

    @else {
      @media screen and (max-width: #{map-get($media-width, $max-media)}px) {
        @content; } } }

  @else {
    @media screen and (min-width: #{map-get($media-width, $min-media) + 1}px) {
      @content; } } }

/**
 * リンクスタイルを、デフォルト下線なし、hover 時下線ありに変更する
 */
@mixin link-decoration_reversal {
  text-decoration: none;

  &:hover {
    text-decoration: underline; } }

/*
 * 矢印アイコン
 *
 * 使用方法: セレクタの疑似要素の中でこの mixin を呼び出す
 *
 * @param { $derection } 矢印の向き (up: 上, down: 下, left, 左, right, 右)
 * @param { $color } 矢印の線の色 (デフォルト: $border-x-dark-gray)
 * @param { $size } 矢印の一辺のサイズ (デフォルト: 7px)
 * @param { $weight } 矢印の線のウェイト (デフォルト: normal)
 */
@mixin iconArrow($direction, $color:$border-x-dark-gray, $size:7px, $weight: normal) {
  content: "";
  width: $size;
  height: $size;
  display: inline-block;
  vertical-align: middle;
  @if $direction == up {
    border: {
      @if $weight == normal {
        left: 1px solid $color;
        top: 1px solid $color; }
      @if $weight == bold {
        left: 2px solid $color;
        top: 2px solid $color; } }
    transform-origin: left top;
    transform: scaleY(0.7) rotate(45deg); }
  @if $direction == down {
    border: {
      @if $weight == normal {
        right: 1px solid $color;
        bottom: 1px solid $color; }
      @if $weight == bold {
        right: 2px solid $color;
        bottom: 2px solid $color; } }
    transform-origin: left top;
    transform: scaleY(0.7) rotate(45deg); }
  @if $direction == left {
    border: {
      @if $weight == normal {
        left: 1px solid $color;
        top: 1px solid $color; }
      @if $weight == bold {
        left: 2px solid $color;
        top: 2px solid $color; } }
    transform-origin: left top;
    transform: scaleX(0.7) rotate(-45deg); }
  @else if $direction == right {
    border: {
      @if $weight == normal {
        right: 1px solid $color;
        bottom: 1px solid $color; }
      @if $weight == bold {
        right: 2px solid $color;
        bottom: 2px solid $color; } }
    transform-origin: left top;
    transform: scaleX(0.7) rotate(-45deg); } }

/**
 * 三角形アイコン
 *
 * 使用方法: セレクタの疑似要素の中でこの mixin を呼び出す
 *
 * @param { $derection } 三角形の向き (up: 上, down: 下, left, 左, right, 右)
 * @param { $color } 三角形の線の色 (デフォルト: #333)
 * @param { $width } 三角形の幅 (デフォルト: 4px)
 * @param { $height } 三角形の高さ (デフォルト: 6px)
 */
@mixin iconTriangle($direction, $color:$bg-x-dark-gray, $width: 8px, $height: 4px) {
  content: '';
  width: 0;
  height: 0;
  display: inline-block;
  vertical-align: middle;
  background-color: transparent;
  border-style: solid;
  @if $direction == up {
    border-width: 0 ($width / 2) $height ($width / 2);
    border-color: transparent transparent $color transparent; }
  @if $direction == down {
    border-width: $height ($width / 2) 0 ($width / 2);
    border-color: $color transparent transparent transparent; }
  @if $direction == left {
    border-width: ($width / 2) $height ($width / 2) 0;
    border-color: transparent $color transparent transparent; }
  @if $direction == right {
    border-width: ($width / 2) 0 ($width / 2) $height;
    border-color: transparent transparent transparent $color; } }

/**
 * action（ボタンスタイル）の disabled 用スタイル
 *
 * @param { $root } BEM の親 Block
 */
@mixin actionDesabled($root) {
  #{$root}--item:disabled {
    color: $font-gray;
    background-color: rgba($bg-gray, 1);

    &:hover {
      opacity: 1; } } }

/**
 * action（ボタンスタイル）の派生系用スタイル
 * ボタンの背景色を引数で指定した色に変更する
 *
 * @param { $bg-color } 背景色
 */
@mixin modifiedActionTrigger($root, $bg-color) {
  #{$root}--item {
    color: $font-white;
    padding: {
      top: 1px;  // 通常ボタンと横並びさせると、ボーダーがない分、着彩ボタンの方が小さくなるので、ボーダー分を padding で確保
      bottom: 1px; }
    border: none;
    background-color: rgba($bg-color, 1.0);

    &:hover {
      opacity: 0.7;
      text-decoration: none; } } }

/**
 * ボタンに矢印を付与する
 *
 * @param { $root } BEM の親 Block
 * @param { $type } backward: 「戻る」用矢印 / forward: 「進む」用矢印
 */
@mixin actionWithArrow($root, $type) {
  $icon-width: 4px;
  $icon-gutter: 20px;
  #{$root}--item {
    position: relative;
    padding-left: $icon-width + $icon-gutter + 10px;
    padding-right: $icon-width + $icon-gutter + 10px;

    @if $type == backward {
      &::before {
        @include iconTriangle(left);
        position: absolute;
        top: 50%;
        left: $icon-gutter;
        transform: translateY(-50%); } }

    @if $type == forward {
      &::after {
        @include iconTriangle(right);
        position: absolute;
        top: 50%;
        right: $icon-gutter;
        transform: translateY(-50%); } } }

  &.is-x-small {
    #{$root}--item {
      $icon-gutter: 10px;
      padding-left: $icon-width + $icon-gutter + 10px;
      padding-right: $icon-width + $icon-gutter + 10px;

      @if $type == backward {
        &::before {
          left: $icon-gutter; } }

      @if $type == forward {
        &::after {
          right: $icon-gutter; } } } } }

/**
 * CSS モジュールにフォントサイズ派生の状態修飾子機能を付与する
 *
 * @param { $element } BEM の親 Block
 * @param { $default-size } ベースとなるフォントサイズ
 */
@mixin fontSizePattern($element, $default-size) {
  $font-sizes : xxBig xBig big xxLarge xLarge large normal small xSmall xxSmall mini xMini xxMini;
  $rate: 0.5;

  @each $font-size in $font-sizes {
    &.is-#{$font-size} {
      #{$element}--item {
        $index_key: index($font-sizes, $font-size);
        font-size: ($default-size + ($rate * ((length($font-sizes) - 1) / 2))) - ($index_key - 1) * $rate; } } }

  @include breakpoint {
    @each $font-size in $font-sizes {
      &.is-#{$font-size} {
        #{$element}--item {
          $index_key: index($font-sizes, $font-size);
          font-size: (($default-size - 0.5) + ($rate * ((length($font-sizes) - 1) / 2))) - ($index_key - 1) * $rate; } } } } }

/**
 * テキストアラインメント状態修飾子拡張
 * モジュールにデフォルトでテキストアラインメントが指定されているものに関して、(詳細度の関係で) ユーティリティモジュールでは上書きできないため、本 mixin で状態修飾子の拡張を行う。
 * 原則、テキストアラインメントはユーティリティに任せるものとし、本 mixin の継承はモジュール自体にアラインメントが指定されている場合のみに限る。
 */
@mixin textAlignment() {
  &.is-left {
    text-align: left; }

  &.is-right {
    text-align: right; }

  &.is-center {
    text-align: center; } }

/**
 * ベースレイアウトスタイル
 * header - image - description - action - footer 構造のベースユニットに対して、基本的なレイアウトスタイルを与える
 * 基本的に画像の位置が上下左右に変動する
 *
 * @param { $image-position } 画像位置。top: 上 / bottom: 下 / left: 左 / right: 右
 * @param { $root } BEM の親 Block
 * @param { $image-width } 画像の横幅
 * @param { $margin-size } 画像位置を left、または right 指定時における、画像とのマージンサイズ
 */
@mixin base-layout($image-position, $root, $image-width:0, $image-height:0, $margin-size:12) {
  @if $image-position == top or $image-position == bottom {
    height: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;

    #{$root}--link {
      display: flex;
      flex-direction: column; } }

  @if $image-position == left or $image-position == right {
    height: auto;
    min-height: #{$image-height}px;
    display: block;
    position: relative;

    #{$root}--link {
      display: block; }

    $image-size: #{$image-width + $margin-size}px;
    @if $image-position == left {
      padding-left: $image-size;

      #{$root}--link {
        margin-left: -($image-size);
        padding-left: $image-size; } }

    @if $image-position == right {
      padding-right: $image-size;

      #{$root}--link {
        margin-right: -($image-size);
        padding-right: $image-size; } }

    #{$root}--image {
      width: #{$image-width}px;
      position: absolute; } }

  #{$root}--link {
    height: 100%;
    min-height: 100%; }

  #{$root}--image {
    @if $image-position == top {
      order: -1; }
    @if $image-position == bottom {
      order: 1; }
    @if $image-position == left {
      left: 0;
      top: 0; }
    @if $image-position == right {
      right: 0;
      top: 0; } } }
// end of base-layout@mixin

/**
 * Vue のローディングアニメーション用画像位置
 *
 * @param { $image-width } 画像の横幅
 * @param { $image-height } 画像の高さ
 */
@mixin loadingImagePosition($image-width, $image-height) {
  > .img {
    position: absolute;
    top: 0;
    left: 0;

    &.is-lazyload,
    &.is-lazyloading {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); } }

  &::before {
    content: "";
    display: block;
    width: 0;
    height: 0;
    padding-top: imageAspectVertical($image-width, $image-height); } }
// end of loadingImagePosition@mixin

/**
 * 閉じる用の「×」ボタンスタイル
 *
 * @param { $icon-size } ボタンアイコンの一辺サイズ
 * @param { $border-color } アイコンの線の色
 * @param { $border-width } アイコンの線の太さ（px）
 */
@mixin closeButtonStyle($icon-size: 16px, $border-color: $border-black, $border-width: 1px) {
  width: $icon-size;
  height: $icon-size;
  display: block;
  overflow: hidden;
  position: relative;
  color: rgba($font-white, 0);
  margin: 0 auto;

  &::before,
  &::after {
    content: '';
    width: $icon-size;
    height: $border-width;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: $border-color; }

  &::before {
    transform: translateX(-50%) rotate(45deg); }

  &::after {
    transform: translateX(-50%) rotate(-45deg); } }

@mixin checkboxStyle() {
  width: 1em;
  height: 1em;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  margin: -2px 4px 0 0;
  background-color: rgba($bg-light-gray, 1);
  border: none;
  border-radius: 50%;

  &::after {
    content: '';
    width: 0.4em;
    height: 0.2em;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-0.3em, -0.25em) rotate(-45deg);
    border-bottom: 2px solid $border-white;
    border-left: 2px solid $border-white; } }
