/*
  #overview
  コンテンツの最大幅制限

  オフセットクラスは、コンテンツ幅を部分的に制限する機能を提供します。
  */
.u-prop {
  /*
    #styleguide
    最大幅 (ピクセル値) サイズ

    ※ 指定値は下限80〜上限850までで、10刻みで指定可能です。

    ```
    <p class="p u-prop__80">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>

    <p class="p u-prop__850">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
    ```
    */
  $minimum-size: 40;
  $maximum-size: 850;
  $size-scale: 10;
  @for $i from 0 through (($maximum-size - $minimum-size) / $size-scale) {
    &__#{$i * $size-scale + $minimum-size} {
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      max-width: #{$i * $size-scale + $minimum-size}px; } } }
