/*
  #styleguide
  共通汎用リスト

  サイト内共通の汎用リストです。

  ```
  <div class="o-list">
    <ul class="o-list--items">
      <li class="o-list--item">Lorem ipsum dolor sit amet, soleat scripta scriptorem cu his. Nec vulputate constituam an, vis ne facilis fierent invidunt.</li>
      <li class="o-list--item">Ut mazim inermis veritus pri, cum eirmod vivendo efficiantur ea, ne vel novum gloriatur. Ne mel argumentum scripserit, sanctus tacimates cu eos, iudico ridens doming an mel.</li>
      <li class="o-list--item">Ea has accusamus comprehensam. At homero percipit usu, vix ex mazim meliore reprehendunt.</li>
    </ul>
  <!-- /.o-list --></div>
  ```
  ```
  <div class="o-list is-circle">
    <ul class="o-list--items">
      <li class="o-list--item">Lorem ipsum dolor sit amet, soleat scripta scriptorem cu his. Nec vulputate constituam an, vis ne facilis fierent invidunt.</li>
      <li class="o-list--item">Ut mazim inermis veritus pri, cum eirmod vivendo efficiantur ea, ne vel novum gloriatur. Ne mel argumentum scripserit, sanctus tacimates cu eos, iudico ridens doming an mel.</li>
      <li class="o-list--item">Ea has accusamus comprehensam. At homero percipit usu, vix ex mazim meliore reprehendunt.</li>
    </ul>
  <!-- /.o-list --></div>
  ```
  */
.o-list {
  $root: &;
  margin-top: 14px;

  &:first-child {
    margin-top: 0; }

  & &--unit {
    margin-top: 14px; }

  &.is-border {
    #{$root}--item {
      margin-top: 1em;
      padding-bottom: 1em;
      border-bottom: 1px solid $border-light-gray;

      &:first-child {
        margin-top: 0; } } }

  $bullet-size: 0.3em;
  $margin-size: 0.6em;
  &.is-circle,
  &.is-rectangle,
  &.is-triangle {
    #{$root}--item,
    #{$root}--term {
      position: relative;
      text-indent: -($bullet-size + $margin-size);
      margin-top: 14px;
      padding-left: ($bullet-size + $margin-size);

      &:first-child {
        margin-top: 0; }

      &::before {
        content: ' ';
        width: $bullet-size;
        height: $bullet-size;
        display: inline-block;
        text-align: left;
        text-indent: 0;
        vertical-align: middle;
        margin: {
          top: -0.2em;
          right: $margin-size; }
        background-color: rgba($bg-black, 1);
        box-sizing: border-box; } }

    #{$root}--term {
      font-weight: bold; }

    #{$root}--description {
      margin: {
        top: 6px;
        left: ($bullet-size + $margin-size); } } }

  &.is-circle {
    #{$root}--item,
    #{$root}--term {
      &::before {
        border-radius: 50%; } } }

  &.is-triangle {
    $triangle-width: 0.3em;
    $triangle-height: 0.4em;

    #{$root}--item,
    #{$root}--term {
      text-indent: -($triangle-width + $margin-size);
      padding-left: ($triangle-width + $margin-size); }

    #{$root}--item::before,
    #{$root}--term::before {
      width: 0;
      height: 0;
      margin: {
        top: -2px;
        right: $margin-size; }
      background-color: transparent;
      border-style: solid;
      border-width: ($triangle-height / 2) 0 ($triangle-height / 2) $triangle-width;
      border-color: transparent transparent transparent $bg-black; }

    #{$root}--description {
      margin-left: ($triangle-width + $margin-size); } }


  @import "_partials/_carouselIndicator.sass";
  @import "_partials/_equipment.sass";
  @import "_partials/_follow.sass";
  @import "_partials/_globalNavigation.sass";
  @import "_partials/_information.sass";
  @import "_partials/_mapData.sass";
  @import "_partials/_note.sass";
  @import "_partials/_ordered.sass";
  @import "_partials/_share.sass";
  @import "_partials/_tag.sass"; }
