&__tag {
  $root: &;

  &.is-home {
    margin-top: 40px;

    &:first-child {
      margin-top: 0; }

    #{$root}--items {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin: -8px 0 0 -8px; }

    #{$root}--item {
      margin: 8px 0 0 8px; } }

  &.is-post {
    #{$root}--items {
      display: flex;
      justify-content: space-around; }

    #{$root}--item {
      opacity: 0.3;

      &.is-active {
        opacity: 1; } }

    @include breakpoint($max-media: sp) {
      #{$root}--items {
        justify-content: flex-start; }

      #{$root}--item {
        margin-left: 10px;

        &:first-child {
          margin-left: 0; } } } }

  &.is-drawerNavigation {
    #{$root}--items {
      display: flex; }

    #{$root}--item {
      margin-left: 30px;

      &:first-child {
        margin-left: 0; } } }

  &.is-footer {
    margin-top: 24px;

    &:first-child {
      margin-top: 0; }

    #{$root}--item {
      margin-top: 10px;

      &:first-child {
        margin-top: 0; } } } }
