/*
  #styleguide
  連番脚注

  直上セクション内の番号付き注釈に対応する連番性の脚注コンポネントです。

  ```
  <div class="o-note__ordered">
    <ul class="o-note__ordered--items">
      <li class="o-note__ordered--item">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</li>
      <li class="o-note__ordered--item">Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</li>
      <li class="o-note__ordered--item">Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</li>
    </ul>
  <!-- /.o-note__ordered --></div>

  <div class="o-note__ordered is-right">
    <ul class="o-note__ordered--items">
      <li class="o-note__ordered--item">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</li>
      <li class="o-note__ordered--item">Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</li>
      <li class="o-note__ordered--item">Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</li>
    </ul>
  <!-- /.o-note__ordered --></div>

  <div class="o-note__ordered is-center">
    <ul class="o-note__ordered--items">
      <li class="o-note__ordered--item">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</li>
      <li class="o-note__ordered--item">Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</li>
      <li class="o-note__ordered--item">Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</li>
    </ul>
  <!-- /.o-note__ordered --></div>
  ```
  */
&__ordered {
  $root: &;
  margin-top: 14px;
  counter-reset: note-order;

  &:first-child {
    margin-top: 0; }

  $note-size: 2.4em;
  & &--item {
    position: relative;
    margin-top: 8px;
    padding-left: $note-size;

    &:first-child {
      margin-top: 0; }

    &::before {
      content: '※' counter(note-order);
      width: $note-size;
      display: inline-block;
      text-align: left;
      margin-left: -#{$note-size};
      counter-increment: note-order; } } }
