/*
  #overview
  文脈画像（アイキャッチ系の画像は含まず）
  */
.o-figure {
  $root: &;
  text-align: center;
  margin: 30px 0;

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  & &--link {
    display: block; }

  & &--item {
    &[data-is-liquid="true"] {
      width: 100%;
      height: auto;
      vertical-align: bottom; } }

  & &--caption {
    display: block;
    font-size: $font-small;
    margin-top: 4px;

    &:first-child {
      margin-top: 0; } }

  @import "_partials/_about.sass";
  @import "_partials/_banner.sass";
  @import "_partials/_carouselThumbnail.sass";
  @import "_partials/_hero.sass";
  @import "_partials/_post.sass";
  @import "_partials/_scale.sass";
  @import "_partials/_video.sass"; }
