&__information {
  $root: &;

  &.is-home {
    $margin-size: 40;
    $margin-percent: getRelativeValue($margin-size, $global-contents-width-pc);
    $block-margin: 40;
    $cols-pc: 4;
    display: flex;
    flex-wrap: wrap;
    margin: #{$block-margin}px auto 0;

    &:first-child {
      margin-top: 0; }

    &.is-left {
      justify-content: flex-start; }

    &.is-center {
      justify-content: center; }

    &.is-right {
      justify-content: flex-end; }

    $col-width: ($global-contents-width-pc - $margin-size * ($cols-pc - 1)) / $cols-pc;
    #{$root}--col {
      width: getRelativeValue($col-width, $global-contents-width-pc);
      margin: #{$margin-size}px 0 0 $margin-percent;
      box-shadow: 0 0 12px rgba($bg-black, 0.4);

      &:nth-child(-n+#{$cols-pc}) {
        margin-top: 0; }

      &:nth-child(#{$cols-pc}n+1) {
        margin-left: 0; } }

    $cols-ph: 2;
    @include breakpoint {
      $margin-size: 30;
      $margin-percent: getRelativeValue($margin-size, $global-contents-width-phablet);
      $block-margin: 30;
      display: flex;
      flex-wrap: wrap;
      margin: #{$block-margin}px auto 0;

      &:first-child {
        margin-top: 0; }

      $col-width: ($global-contents-width-phablet - $margin-size * ($cols-ph - 1)) / $cols-ph;
      #{$root}--col {
        width: getRelativeValue($col-width, $global-contents-width-phablet);
        margin: #{$margin-size}px 0 0 $margin-percent;

        &:nth-child(-n+#{$cols-pc}) {
          margin-top: #{$margin-size}px; }

        &:nth-child(#{$cols-pc}n+1) {
          margin-left: $margin-percent; }

        &:nth-child(-n+#{$cols-ph}) {
          margin-top: 0; }

        &:nth-child(#{$cols-ph}n+1) {
          margin-left: 0; } } }

    @include breakpoint($max-media: sp) {
      $margin-size: 20;
      $margin-percent: getRelativeValue($margin-size, $global-contents-width-sp);
      $block-margin: 20;
      display: block;
      margin: #{$block-margin}px auto 0;

      &:first-child {
        margin-top: 0; }

      #{$root}--col {
        width: auto;
        margin: #{$margin-size}px auto 0;

        &:nth-child(-n+#{$cols-pc}) {
          margin-top: #{$margin-size}px; }

        &:nth-child(#{$cols-pc}n+1) {
          margin-left: 0; }

        &:nth-child(-n+#{$cols-ph}) {
          margin-top: #{$margin-size}px; }

        &:nth-child(#{$cols-ph}n+1) {
          margin-left: 0; }

        &:first-child {
          margin-top: 0; } } } } }
