&__footerNavigation {
  $root: &;
  $col-width-px: ($global-contents-width-pc / 5);
  display: flex;
  justify-content: center;

  & &--col {
    width: getRelativeValue($col-width-px, $global-contents-width-pc);
    padding: 5px 20px;
    border-left: 1px solid $border-dark-navy;

    &:first-child {
      border-left: none; } }

  @include breakpoint {
    display: block;

    & &--col {
      width: auto;
      padding: #{$global-section-vertical-prop-sp}px 0;
      border: {
        top: 1px solid $border-dark-navy;
        left: none; }

      &:first-child {
        padding-top: 0;
        border-top: none; }

      &:last-child {
        padding-bottom: 0; } } } }
