/*
  #overview
  オブジェクトの土台となるレイアウト
  */
.l-unit {
  $root: &;

  margin-top: 30px;

  &:first-child {
    margin-top: 0; }

  & > &--description,
  & > &--footer,
  & > &--action {
    margin-top: 20px;

    &:first-child {
      margin-top: 0; } }

  &.is-border {
    > #{$root}--header,
    > #{$root}--description,
    > #{$root}--action,
    > #{$root}--footer {
      margin-top: 0;
      padding: 30px #{$global-prop-pc}px;
      border-top: 1px solid $border-light-gray;

      &:first-child {
        padding-top: 0;
        border-top: none; }

      &:last-child {
        padding-bottom: 0; } } }

  &.is-box {
    border: 1px solid $border-light-gray;

    > #{$root}--header,
    > #{$root}--description,
    > #{$root}--footer {
      margin-top: 0;
      padding: 20px;
      border-top: 1px solid $border-light-gray;

      &:first-child {
        border-top: none; } } }

  @import "_partials/_about.sass";
  @import "_partials/_calendar.sass";
  @import "_partials/_carousel.sass";
  @import "_partials/_drawerNavigation.sass";
  @import "_partials/_information.sass";
  @import "_partials/_modal.sass"; }
