&__about {
  $root: &;
  margin: 80px 0;

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  & &--item {
    box-shadow: 40px 40px rgba($bg-black, 0.2);

    &[data-is-liquid="true"] {
      width: 100%;
      height: auto;
      vertical-align: bottom; } }

  @include breakpoint($max-media: pc) {
    #{$root}--item {
      box-shadow: 20px 20px rgba($bg-black, 0.2); } }

  @include breakpoint($max-media: sp) {
    #{$root}--item {
      box-shadow: none; } } }
