/*
  #styleguide
  モーダルウィンドウ

  vue component@/scripts/es6/_components/_molecules/Modal.vue

  ```
  <Modal :is-show.sync="showModal">
    <p class="p">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
  </Modal>
  ```
  */
&__modal {
  $root: &;
  width: 100%;
  max-width: 480px;
  min-height: 56px;
  max-height: 100%;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  padding: 40px 0px;
  background-color: rgba($bg-white, 1);
  border-radius: $border-radius;
  box-shadow: 6px 6px 14px rgba($bg-black, 0.5);

  &.is-mini {
    max-width: 240px; }

  &.is-small {
    max-width: 320px; }

  &.is-large {
    max-width: 640px; }

  &.is-huge {
    max-width: 1600px; }

  $gutter-size: 30px;
  & &--header,
  & &--description,
  & &--action,
  & &--footer {
    margin-top: $gutter-size;
    padding: {
      left: $gutter-size;
      right: $gutter-size; }

    &:first-child {
      margin-top: 0; } }

  & &--description {
    flex-grow: 1;
    overflow-y: auto; }

  & &--action {
    $button-height: 41px;  // デフォルトの o-action の height 値
    min-height: $button-height;  // description に内包されたコンテンツが超過し、且つ action も保有しているモーダルの場合、description と action の両方にスクロールが発生してしまうため、ボタン分の高さを予め確保し、description + action（ボタン 1 行分のみ）の場合は action はスクロールしないようにする
    overflow-y: auto;

    &.is-close {
      min-height: 0;
      position: absolute;
      top: 5px;
      right: 8px;
      margin-top: 0;
      padding: 0;
      border: none;
      z-index: 21; } }

  &.is-border {
    $gutter-size: 30px;

    #{$root}--header,
    #{$root}--description,
    #{$root}--action,
    #{$root}--footer {
      padding: {
        top: $gutter-size; }
      border-top: 1px solid $border-gray;

      &:first-child {
        padding-top: 0;
        border-top: none; } }

    // 詳細度の関係でガターが入ってしまうので、再上書き
    #{$root}--action {
      &.is-close {
        margin-top: 0;
        padding: 0;
        border: none; } } }

  &.is-lite {
    padding: 0;

    #{$root}--description {
      padding: {
        left: 0;
        right: 0; } } } }
