&__carousel {
  $root: &;

  position: relative;
  margin-top: 40px;

  &:first-child {
    margin-top: 0; }

  &.is-home {
    // 全体の黒い半透過レイヤー
    &::before {
      content: "";
      width: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($bg-black, 0);
      z-index: 3; }

    #{$root}--description {
      // カルーセル下部の白いブラー（黒系の画像に黒字ロゴが乗っかった時の可読性担保用）
      &::before {
        content: "";
        width: 200%;
        height: 100%;
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 3; } } }

  &.is-about {
    width: 100%;
    position: fixed;

    // 全体の黒い半透過レイヤー
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($bg-black, 0.2);
      z-index: 3; } }

  & &--description {
    position: relative;
    overflow: hidden; }

  & &--action {
    // prev / next コントローラ
    &.is-controller {
      height: 64px;
      width:  44px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 3;

      &.is-prev {
        left: 0; }

      &.is-next {
        right: 0; } }

    // インジケータ
    &.is-indicator {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 16px;
      margin: auto;
      z-index: 3; }

    // サムネイル
    &.is-thumbnail {
      margin-top: 20px;
      padding: 0 #{$global-section-horizontal-prop-pc}px; } } }
