.l-cols {
  $root: &;
  $margin-size: 40;
  $margin-percent: getRelativeValue($margin-size, $global-contents-width-pc);
  $block-margin: 40;
  $element-margin: 40;
  display: flex;
  flex-wrap: wrap;
  margin: #{$block-margin - $element-margin}px 0 0 (-#{$margin-size}px);

  &:first-child {
    margin-top: -#{$element-margin}px; }

  &.is-left {
    justify-content: flex-start; }

  &.is-center {
    justify-content: center; }

  &.is-right {
    justify-content: flex-end; }

  &.is-vCenter {
    align-items: center; }

  &.is-baseline {
    align-items: baseline; }

  & > &--col {
    margin: #{$element-margin}px 0 0 #{$margin-size}px; }

  @for $i from 1 through 4 {
    &.is-col#{$i} {
      margin: #{$block-margin}px auto 0;

      &:first-child {
        margin-top: 0; }

      $col-width: ($global-contents-width-pc - $margin-size * ($i - 1)) / $i;
      > #{$root}--col {
        width: getRelativeValue($col-width, $global-contents-width-pc);
        margin-left: $margin-percent;

        &:nth-child(-n+#{$i}) {
          margin-top: 0; }

        &:nth-child(#{$i}n+1) {
          margin-left: 0; } }

      &[data-is-breakpoint="pc"] {
        @include breakpoint($max-media: pc) {
          > #{$root}--col {
            width: 100%;
            margin: {
              top: #{$global-margin-sp}px;
              left: 0; }

            &:first-child {
              margin-top: 0; } } } }

      &[data-is-breakpoint="tablet"] {
        @include breakpoint($max-media: tablet) {
          > #{$root}--col {
            width: 100%;
            margin: {
              top: #{$global-margin-sp}px;
              left: 0; }

            &:first-child {
              margin-top: 0; } } } }

      &[data-is-breakpoint="phablet"] {
        @include breakpoint($max-media: phablet) {
          > #{$root}--col {
            width: 100%;
            margin: {
              top: #{$global-margin-sp}px;
              left: 0; }

            &:first-child {
              margin-top: 0; } } } }

      &[data-is-breakpoint="sp"] {
        @include breakpoint($max-media: sp) {
          > #{$root}--col {
            width: 100%;
            margin: {
              top: #{$global-margin-sp}px;
              left: 0; }

            &:first-child {
              margin-top: 0; } } } } } }

  &.is-border {
    $gutter-size: 10;
    $gutter-percent: getRelativeValue($gutter-size, $global-contents-width-pc);
    margin: {
      right: (-#{$gutter-size}px);
      left: 0; }

    > #{$root}--col {
      position: relative;
      margin: {
        right: #{$gutter-size}px;
        left: 0; }
      padding-right: #{$gutter-size}px;

      &::after {
        content: '';
        width: 0;
        height: 50%;
        position: absolute;
        top: 50%;
        right: 0;
        display: block;
        transform: translateY(-50%);
        border-right: 1px solid $border-gray; }

      &:last-child {
        padding-right: 0;

        &::after {
          display: none; } } }

    @for $i from 1 through 4 {
      &.is-col#{$i} {
        margin: {
          right: auto;
          left: auto; }

        $col-width: ($global-contents-width-pc - $gutter-size * ($i * 2 - 2)) / $i;
        > #{$root}--col {
          width: getRelativeValue($col-width, $global-contents-width-pc);
          margin: {
            left: 0;
            right: 0; }
          padding: {
            left: $gutter-percent;
            right: $gutter-percent; }
          box-sizing: content-box;

          // 最上段カラム
          &:nth-child(-n+#{$i}) {
            margin-top: 0; }

          // 最右カラム
          &:nth-child(#{$i}n) {
            padding-right: 0;

            &::after {
              display: none; } }

          // 最左カラム
          &:nth-child(#{$i}n+1) {
            padding-left: 0; } } } } }

  &.is-box {
    > #{$root}--col {
      padding: #{$margin-size}px;
      border: 1px solid $border-gray; } }

  &.is-comma {
    margin: 4px 0 0 0;

    &:first-child {
      margin-top: 0; }

    #{$root}--col {
      margin: 0;

      &::after {
        content: attr(data-comma);  // 親に一括で設定したいが、親の data 属性の値を子で content で受けられないのでやむを得ず子に個別に設定する
        display: inline; }

      &:last-child {
        &::after {
          display: none; } } } }

  @import "_partials/_calendar.sass";
  @import "_partials/_carousel.sass";
  @import "_partials/_follow.sass";
  @import "_partials/_footerNavigation.sass";
  @import "_partials/_header.sass";
  @import "_partials/_information.sass";
  @import "_partials/_monthController.sass";
  @import "_partials/_postFooter.sass";
  @import "_partials/_week.sass"; }
