&__monthController {
  $root: &;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  margin-top: 20px;

  &:first-child {
    margin-top: 0; }

  & &--col {
    &.is-date {
      margin: 0 40px; }

    &.is-prev {
      order: -1; }

    $action-width: 74;
    &.is-prev,
    &.is-next {
      width: getRelativeValue($action-width, $global-contents-width-pc); } }

  $modal-close-button-width: 22px;
  $block-prop: 20px;
  &.is-modal {
    margin: {
      left: $modal-close-button-width + $block-prop;
      right: $modal-close-button-width + $block-prop; } } }
