&__globalNavigation {
  $root: &;

  &.is-drawer {
    #{$root}--item {
      margin-top: 40px;

      &:first-child {
        margin-top: 0; } }

    @include breakpoint {
      #{$root}--item {
        margin-top: 30px; } } }

  &.is-header {
    height: 100%;

    #{$root}--items {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-around; }

    #{$root}--item {
      height: 100%; } }

  &.is-footer {
    width: 100%;
    height: 100%;

    #{$root}--items {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center; }

    #{$root}--item {
      height: 100%;
      margin-left: 6em;

      &:first-child {
        margin-left: 0; } } }

  &.is-post {
    width: 100%;
    display: table;

    #{$root}--unit {
      display: table-cell; }

    #{$root}--term {
      display: none; }

    @include breakpoint($max-media: sp) {
      display: block;
      margin-top: 30px;

      &:first-child {
        margin-top: 0; }

      #{$root}--unit {
        display: flex;
        align-items: center;
        margin-top: 10px;
        padding-top: 10px;
        border-top: 1px solid $border-light-gray;

        &:first-child {
          margin-top: 0;
          padding-top: 0;
          border-top: none; } }

      #{$root}--term {
        width: 6em;
        display: block;
        font-size: $font-small;
        font-weight: bold; }

      #{$root}--description {
        margin-left: 20px; } } } }

