// ユークリッド互除法を用いて、二つの自然数 a, b の最大公約数を求めます。
@function gcd($a, $b) {
	$gcd: 0;
	$A: $a;
	$B: $b;
	$x: 0;
	$y: 0;

	@while $gcd == 0 {
		@if $A > $B {
			$x: $A;
			$y: $B; }
		@else if $A < $B {
			$x: $B;
			$y: $A; }
		@else {
			$gcd: $A; }

		$z: ($x - $y);
		$A: $y;
		$B: $z; }

	@return $gcd; }

// 画像のアスペクト比を算出します。
@function aspect($width, $height, $side) {
	$gcd: gcd($width, $height);

	@if $side == 'width' {
		@return ($width / $gcd); }
	@else if $side == 'height' {
		@return ($height / $gcd); } }

@function imageAspectVertical($width, $height) {
	@return (aspect($width, $height, 'height') / aspect($width, $height, 'width')) * 100%; }

/*
 * 横方向の相対値サイズを算出
 *
 * 使用方法: 相対値を与えたいプロパティで呼び出す
 *
 * @param $numerator: 相対値を求めたいサイズ
 * @param $denominator: 相対値計算の基準となるサイズ
 * @return {int} 算出相対値
 */
@function getRelativeValue($numerator, $denominator) {
  @return ($numerator / $denominator * 100%); }

/**
 * 文字列を数値に変換
 */
@function toNumber($string) {
  $result: 0;
  $decimal: 1;
  $minus: false;
  $float: false;

  $len: str-length($string);

  $map: ('0': 0, '1': 1, '2': 2, '3': 3, '4': 4);
  $map: map-merge($map, ('5': 5, '6': 6, '7': 7, '8': 8, '9': 9));

  @for $i from 1 through $len {
    $character: str-slice($string, $i, $i);
    $number: map-get($map, $character);

    @if $character == '-' {
      $minus: true; }
    @else if $character == '.' {
      $float: true; }
    @else {
      $result: $result * 10 + $number;

      @if $float {
        $decimal: $decimal * 10; } } }

  @if not $minus {
    $result: $result / $decimal; }
  @else {
    $result: -($result / $decimal); }

  @return $result; }
