&__tag {
  $root: &;

  &.is-footer {
    #{$root}--link {
      color: $font-white;
      text-decoration: none;

      &:hover {
        > #{$root}--label {
          &.is-primary {
            text-decoration: underline; } } } }

    #{$root}--label {
      display: block;
      font-weight: normal;
      text-align: center;

      &.is-primary {
        font-size: $font-large; }

      &.is-secondary {
        color: $font-gray;
        font-size: $font-small; } }

    @include breakpoint {
      display: inline-block;

      #{$root}--link {
        display: flex;
        flex-direction: column;
        align-items: center; }

      #{$root}--label {
        display: inline; } } } }
