&__video {
  $root: &;
  &.is-youtube {
    $aspect-ratio-horizontal: 16;
    $aspect-ratio-vertical: 9;

    height: 0;
    position: relative;
    overflow: hidden;
    margin: 30px 0;
    padding-bottom: getRelativeValue($aspect-ratio-vertical, $aspect-ratio-horizontal);

    &:first-child {
      margin-top: 0; }

    &:last-child {
      margin-bottom: 0; }

    #{$root}--item {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      border: none; } } }
