&__carousel {
  $root: &;
  display: flex;
  box-sizing: content-box;

  &.is-fade {
    width: 100%;
    height: auto;
    display: block;
    position: relative;

    #{$root}--col {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transform: scale(1);
      z-index: 0;

      &.is-fade {
        animation: fade 10s ease; }

      &.is-zoom {
        animation: zoom 7s linear; }

      &.is-fade.is-zoom {
        animation: fade 2s ease, zoom 7s linear;
 }        // transition: opacity 2s ease, transform 7s linear

      &.is-prev {
        opacity: 1;
        z-index: 1; }

      &.is-active {
        position: absolute;
        opacity: 1;
        z-index: 2; } } }

  &.is-pseudo {
    display: block;
    opacity: 0; } }

@keyframes zoom {
  0% {
    transform: scale(1); }
  100% {
    transform: scale(1.15); } }

@include breakpoint {
  @keyframes zoom {
    0% {
      transform: scale(1); }
    100% {
      transform: scale(1.3); } } }

@keyframes fade {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
