@mixin baseSectionStyle {
  $root: &;
  position: relative;

  & &--body {
    width: 100%;
    max-width: #{$global-contents-width-pc}px;
    margin: 0 auto;
    padding: #{$global-margin-pc}px #{$global-prop-pc}px;

    @include breakpoint {
      width: 100%;
      padding: #{$global-margin-sp}px #{$global-prop-sp}px; } } }

.f-section {
  @include baseSectionStyle;

  &.is-fill__white,
  &.is-fill__gray {
    margin: 0;
    padding: #{$global-section-vertical-prop-pc}px 0; }

  &.is-fill__white {
    background-color: rgba($bg-white, 1); }

  &.is-fill__gray {
    background-color: rgba($bg-gray, 1); }

  &.is-fill__darkgray {
    background-color: rgba($bg-xx-dark-navy, 1); }

  /**
   * ヘッダーメイン領域
   */
  &__header {
    min-height: #{$header-height-pc}px;
    display: flex;
    flex: 0 0 auto;
    flex-direction: column;
    justify-content: center;
    padding: 10px 0;

    & &--body {
      width: 100%;
      max-width: #{$global-contents-width-pc}px;
      margin: 0 auto; }

    @include breakpoint {
      min-height: #{$header-height-sp}px; } }

  &__drawerNavigation {
    width: 640px;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    background-color: rgba($bg-xx-dark-navy, 1);
    z-index: 21; // z-index の値は wiki 参照
    box-shadow: 0 0 12px 8px rgba($bg-black, 0.3);

    & &--body {
      width: 100%;
      max-width: #{$global-contents-width-pc}px;
      margin: 0 auto; }

    @include breakpoint($max-media: tablet) {
      width: 100%;
      box-shadow: none; } }

  /**
   * フッターナビゲーション
   */
  &__footerNavigation {
    position: relative;
    padding: 40px #{$global-prop-pc}px;
    background-color: rgba($bg-black, 0.4);

    & &--body {
      max-width: #{$global-contents-width-pc}px;
      position: relative;
      margin: 0 auto; }

    @include breakpoint {
      padding: 20px #{$global-prop-sp}px; } }

  /**
   * フッターメイン領域
   */
  &__footer {
    position: relative;
    background-color: rgba($bg-white, 1);
    z-index: 5;

    & &--body {
      position: relative;
      margin: 0 auto;
      padding: 30px 0;
      border-top: 1px solid $border-light-gray;

      &:first-child {
        border-top: none; } }

    @include breakpoint {
      padding: #{$global-margin-sp}px #{$global-prop-pc}px; } }

  &__home {
    $root: &;
    @include baseSectionStyle;

    &.is-youtube {
      background-color: rgba($bg-gray, 1); }

    &.is-promotion {
      background-color: rgba($bg-x-light-gray, 1); }

    &.is-information {
      background-color: rgba($bg-gray, 1);

      &::before {
        content: "";
        width: 100%;
        height: 10px;
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        background: linear-gradient(rgba($bg-black, 0.1), rgba($bg-black, 0)); } } }

  &__about {
    $root: &;
    @include baseSectionStyle;
    background-color: rgba($bg-xx-dark-navy, 1);

    // 画像のシャドウのはみ出しサイズ
    $image-shadow-extrusion-size: 40;
    &:last-child {
      #{$root}--body {
        padding-bottom: #{$global-margin-pc + $image-shadow-extrusion-size}px; } }

    // 画像のシャドウのはみ出しサイズ
    $image-shadow-extrusion-size: 20;
    @include breakpoint($max-media: pc) {
      &:last-child {
        #{$root}--body {
          padding-bottom: #{$global-margin-pc + $image-shadow-extrusion-size}px; } } }

    @include breakpoint {
      &:last-child {
        #{$root}--body {
          padding-bottom: #{$global-margin-sp + $image-shadow-extrusion-size}px; } } }

    @include breakpoint($max-media: sp) {
      &:last-child {
        #{$root}--body {
          padding-bottom: #{$global-margin-sp}px; } } } }

  &__tools {
    $root: &;
    @include baseSectionStyle;
    box-shadow: 0 -10px 40px rgba($bg-black, 0.5);

    &.is-calendar {
      background: linear-gradient(to top right, rgba($bg-xx-dark-gray, 1), rgba($bg-x-dark-gray, 1)); }

    // 制圧戦
    &.is-secure {
      background: linear-gradient(to top right, rgba($bg-xx-dark-red, 1), rgba($bg-x-dark-gray, 1)); }

    // シールロック
    &.is-seize {
      background: linear-gradient(to top right, rgba($bg-xx-dark-yellow, 1), rgba($bg-x-dark-gray, 1)); }

    // 砕氷戦
    &.is-shatter {
      background: linear-gradient(to top right, rgba($bg-xx-dark-blue, 1), rgba($bg-x-dark-gray, 1)); }

    // オンサル・ハカイル
    &.is-danshigNaadam {
      background: linear-gradient(to top right, rgba($bg-xx-dark-green, 1), rgba($bg-x-dark-gray, 1)); } }

  &__post {
    position: relative;
    padding: 0 #{$global-prop-pc}px;
    z-index: 1;

    & &--body {
      width: 100%;
      max-width: #{$global-contents-width-pc}px;
      display: flex;
      align-items: flex-start;
      margin: 0 auto; }

    @include breakpoint($max-media: tablet) {
      & &--body {
        display: block; } } }

  /**
   * オーバーレイコンテンツ用下地
   */
  &__overlay {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;

    & &--body {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: stretch;
      flex-direction: column; } }

  /**
   * グローバルナビゲーション用
   */
  &__globalNavigation {
    $root: &; }

  &__breadcrumb {
    $root: &; }

  &__hero {
    $root: &;
    position: relative;

    & &--body {
      position: relative; }

    &.is-home {
      #{$root}--body {
        overflow: hidden;
        position: relative; } }

    &.is-about {
      #{$root}--body {
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        margin-top: -#{$header-height-pc}px; }

      @include breakpoint($max-media: tablet) {
        #{$root}--body {
          height: 50vh; } }

      @include breakpoint($max-media: phablet) {
        #{$root}--body {
          margin-top: -#{$header-height-sp}px; } } } }

  &__profile {
    padding: 30px 0;
    background-color: rgba($bg-white, 1);
    border-radius: 10px;
    filter: drop-shadow(0 4px 3px rgba(0, 0, 0, 0.4));

    @include breakpoint($max-media: tablet, $min: true, $min-media: sp) {
      width: 100%;
      max-width: 640px;
      overflow: hidden;
      margin: 0 auto;
      padding: 0; } }

  &__postHeader {
    margin-bottom: 30px; }

  &__share {
    width: 40px;
    height: 100%;
    position: fixed;
    // position: sticky
    top: 50%;
    // bottom: 50%
    right: 0;
    transform: translateY(-50%);
    // transform: translateY(50%)
    // padding-right: 10px
    z-index: 5;
    background-color: rgba($bg-xx-dark-navy, 0);

    & &--body {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }

    &.is-stack {
      position: absolute;
      bottom: -#{$global-margin-pc}px;
      transform: none; }

    @include breakpoint($max-media: tablet) {
      display: none; } }

  &__adsense {
    margin-top: #{$global-margin-pc}px;
    padding: #{$global-margin-pc}px;
    border: 1px solid $border-light-gray;

    &:first-child {
      margin-top: 0; }

    @include breakpoint($max-media: tablet) {
      margin-top: #{$global-margin-sp}px;
      padding: #{$global-margin-sp}px;

      &:first-child {
        margin-top: 0; } } } }
