&__mapData {
  $root: &;
  margin-top: 20px;

  &:first-child {
    margin-top: 0; }

  // description にセレクター等のアクション要素が内包される場合
  &.is-center__vertical {
    #{$root}--unit {
      align-items: center; } }

  &.is-cologne {
    #{$root}--unit {
      display: flex;
      justify-content: flex-start;
      margin-top: 6px;

      &:first-child {
        margin-top: 0; } }

    #{$root}--term {
      white-space: nowrap;

      &::after {
        content: '：'; } }

    #{$root}--description {
      flex-grow: 1; }

    &.is-right {
      #{$root}--unit {
        justify-content: flex-end; }

      #{$root}--description {
        flex-grow: 0; } }

    &.is-center {
      #{$root}--unit {
        justify-content: center; }

      #{$root}--description {
        flex-grow: 0; } } }

  &.is-table {
    width: 100%;
    display: table;
    border-collapse: collapse;

    > #{$root}--unit {
      display: table-row;

      > #{$root}--term,
      > #{$root}--description {
        display: table-cell; }

      > #{$root}--description {
        padding-left: 2em; } }

    &.is-left {
      width: auto;
      margin-right: auto;
      margin-left: 0; }

    &.is-right {
      width: auto;
      margin-right: 0;
      margin-left: auto; }

    &.is-center {
      width: auto;
      margin: {
        left: auto;
        right: auto; } } }

  &.is-border {
    #{$root}--unit {
      margin-top: 0;
      border-top: 1px solid $border-light-gray;

      &:first-child {
        border-top: none; }

      &:last-child {
        #{$root}--term,
        #{$root}--description {
          padding-bottom: 0; } } }

    // is-table 時の併用を想定して、unit に付与せずに子要素に一つずつ padding 設定する
    // （display: table 時に table-row には padding 付与されないため）
    #{$root}--term,
    #{$root}--description {
      padding: 1em 0.1em; } } }

