&__follow {
  $root: &;

  &.is-profile {
    $profile-width: 280;
    $margin-size: 20;
    $cols: 2;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 30px;

    &:first-child {
      margin-top: 0; }

    #{$root}--col {
      width: (100% / $cols);
      text-align: center;
      margin-top: #{$margin-size}px;
      padding: 0 getRelativeValue($margin-size, $profile-width);

      &:nth-child(-n+#{$cols}) {
        margin-top: 0; }

      &:nth-child(#{$cols}n+1) {
        border-right: 1px solid $border-x-dark-navy; } }

    @include breakpoint($max-media: tablet, $min: true, $min-media: sp) {
      #{$root}--col {
        &:nth-child(#{$cols}n) {
          text-align: left;
          padding: 0 0 0 20px; }

        &:nth-child(#{$cols}n+1) {
          text-align: right;
          padding: 0 20px 0 0; } } }

    @include breakpoint($min: true, $min-media: sp) {
      flex-direction: column;

      #{$root}--col {
        width: auto;
        padding: 0;
        border-right: none;

        &:nth-child(-n+#{$cols}) {
          margin-top: #{$margin-size}px; }

        &:nth-child(#{$cols}n) {
          text-align: center;
          padding: 0; }

        &:nth-child(#{$cols}n+1) {
          text-align: center;
          padding: 0;
          border-right: none; }

        &:first-child {
          margin-top: 0; } } } } }
