&__hero {
  $root: &;

  &.is-home {
    #{$root}--item {
      height: 100vh;
      background: {
        position: center center;
        size: cover;
        repeat: no-repeat; } } }

  &.is-about {
    #{$root}--item {
      height: 100vh;
      background: {
        position: center center;
        size: cover;
        repeat: no-repeat;
        attachment: fixed; } }

    @include breakpoint($max-media: tablet) {
      #{$root}--item {
        height: 50vh; } } } }

