&__about {
  $root: &;

  // 画像のシャドウのはみ出しサイズ
  $image-shadow-extrusion-size: 40;
  $margin-size: 140;

  margin-top: #{$margin-size + $image-shadow-extrusion-size}px;
  flex-direction: column;
  justify-content: center;

  &:first-child {
    margin-top: 0; }

  $image-width: 370;
  $image-height: 485;
  $prop: 80;
  &.is-left {
    @include base-layout($image-position:left, $root:$root, $image-width:$image-width, $image-height:$image-height, $margin-size:$prop);
    display: flex; }

  &.is-right {
    @include base-layout($image-position:right, $root:$root, $image-width:$image-width, $image-height:$image-height, $margin-size:$prop);
    display: flex; }

  & &--description {
    margin-top: #{$prop}px; }

  &::before {
    box-shadow: 0 0 40px 10px rgba($bg-black, 0.2) inset; }

  // 画像のシャドウのはみ出しサイズ
  $image-shadow-extrusion-size: 20;
  @include breakpoint($max-media: pc) {
    $image-width: 280;
    $image-height: 367;
    $prop: 40;

    margin-top: #{$margin-size + $image-shadow-extrusion-size}px;

    &.is-left {
      @include base-layout($image-position:left, $root:$root, $image-width:$image-width, $image-height:$image-height, $margin-size:$prop);
      display: flex; }

    &.is-right {
      @include base-layout($image-position:right, $root:$root, $image-width:$image-width, $image-height:$image-height, $margin-size:$prop);
      display: flex; }

    #{$root}--description {
      margin-top: #{$prop}px; } }

  @include breakpoint($max-media: tablet) {
    $margin-size: 100;
    margin-top: #{$margin-size + $image-shadow-extrusion-size}px; }

  @include breakpoint($max-media: sp) {
    $margin-size: 60;
    $prop: 60;

    margin-top: #{$margin-size + $image-shadow-extrusion-size}px;
    &.is-left,
    &.is-right {
      min-height: 0;
      padding: 0;

      #{$root}--image {
        width: 100%;
        position: static;
        order: -1; }

      #{$root}--header,
      #{$root}--description {
        margin-top: #{$prop}px; } } } }
