&__equipment {
  $root: &;

  &.is-post {
    $cols: 2;
    $wrapper-width: 680;
    $margin-size: 40;
    $unit-width: (($wrapper-width - $margin-size * ($cols - 1)) / $cols);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    $prop-size: 30;
    #{$root}--unit {
      width: getRelativeValue($unit-width, $wrapper-width);
      display: flex;
      align-items: center;
      margin-top: #{$prop-size}px;
      padding-bottom: #{$prop-size}px;
      border-bottom: 1px solid $border-light-gray;

      &:nth-child(-n+#{$cols}) {
        margin-top: 0; } }

    #{$root}--description {
      margin-left: #{$prop-size}px; }

    @include breakpoint {
      display: block;

      #{$root}--unit {
        width: auto;

        &:nth-child(-n+#{$cols}) {
          margin-top: #{$prop-size}px; }

        &:first-child {
          margin-top: 0; } } } } }
