&__follow {
  $root: &;

  &.is-header {
    height: 100%;

    #{$root}--items {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end; }

    #{$root}--item {
      height: 100%;
      margin-left: 20px;

      &:first-child {
        margin-left: 0; } } }

  &.is-footer {
    #{$root}--items {
      display: flex;
      align-items: center;
      justify-content: center; }

    #{$root}--item {
      margin-left: 20px;

      &:first-child {
        margin-left: 0; } } } }
