&__equipment {
  $root: &;

  &.is-post {
    #{$root}--item {
      margin-top: 4px;

      &:first-child {
        margin-top: 0; }

      &.is-name {
        font-size: $font-large;
        font-weight: bold; }

      &.is-color {
        color: $font-gray;
        font-size: $font-small; }

      &.is-channel {
        font-size: $font-small; } } } }
