.f-aside {
  &__post {
    $root: &;
    $width: 280;

    width: getRelativeValue($width, $global-contents-width-pc);
    position: relative;
    margin: {
      top: #{$global-margin-pc}px;
      left: getRelativeValue($global-margin-pc, $global-contents-width-pc); }
    padding: 0;

    @include breakpoint($max-media: tablet) {
      width: auto;
      margin-left: 0; } } }
