&__monthController {
  $root: &;
  margin-top: 10px;

  &:first-child {
    margin-top: 0; }

  & &--item {
    width: 100%;
    height: 100%;
    position: relative;
    display: block; }

  &.is-prev,
  &.is-next {
    #{$root}--item {
      color: rgba($font-white, 0);
      overflow: hidden;
      white-space: nowrap;

      &::before {
        position: absolute;
        top: 50%;
        left: 50%; } } }

  &.is-prev {
    #{$root}--item {
      &::before {
        @include iconTriangle(left, $bg-white, 24px, 12px);
        transform: translate(-50%, -50%); } } }

  &.is-next {
    #{$root}--item {
      &::before {
        @include iconTriangle(right, $bg-white, 24px, 12px);
        transform: translateY(-50%); } } }

  &.is-current {
    #{$root}--item {
      color: $font-white;
      font-size: $font-x-small;
      text-align: center;
      padding-bottom: 0.3em;

      &::before {
        content: '';
        width: 0;
        height: 1px;
        display: block;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        background-color: rgba($bg-white, 0);
        transition: all 0.2s ease-in; }

      &:hover {
        &::before {
          width: 100%;
          background-color: rgba($bg-white, 0.5); } }

      &:disabled {
        opacity: 0.2;

        &::before {
          display: none; } } } } }
