/*
  #overview
  メディアクエリユーティリティ
  */
.u-media {
  // SP サイズ未満のときに表示
  &__ltSP {
    display: none;

    @include breakpoint($max-media: sp) {
      display: block; } }

  // Phablet サイズ未満のときに表示
  &__ltPH {
    display: none;

    @include breakpoint($max-media: phablet) {
      display: block; } }

  // Tablet サイズ未満のときに表示
  &__ltTB {
    display: none;

    @include breakpoint($max-media: tablet) {
      display: block; } }

  // PC サイズ未満のときに表示
  &__ltPC {
    display: none;

    @include breakpoint($max-media: pc) {
      display: block; } }

  // SP サイズ以上のときに表示
  &__gtSP {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: sp) {
      display: block; } }

  // Phablet サイズ以上のときに表示
  &__gtPH {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: phablet) {
      display: block; } }

  // Tablet サイズ以上のときに表示
  &__gtTB {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: tablet) {
      display: block; } }

  // PC サイズ以上のときに表示
  &__gtPC {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: pc) {
      display: block; } } }
