&__hamburger {
  $root: &;
  $button-width: 24px;
  $button-height: 18px;
  $border-weight: 2px;

  & &--item {
    width: $button-width;
    height: $button-height;
    overflow: hidden;
    display: block;
    position: relative;
    color: rgba($font-white, 0);
    border: {
      top: $border-weight solid $border-white;
      bottom: $border-weight solid $border-white; }
    transition: opacity 0.5s ease;

    &::before {
      content: "";
      width: 100%;
      height: $border-weight;
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: $border-white; }

    &:hover {
      opacity: 0.7; } } }
