&__post {
  $root: &;
  text-align: center;
  margin: 30px 0;

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  & &--link {
    display: block; }

  & &--item {
    &[data-is-liquid="true"] {
      width: 100%;
      height: auto;
      vertical-align: bottom; } } }
