/*
  #overview
  ブロック要素の表示制御

  継承したブロック要素に対して表示制御をかけます。
  */
.u-block {
  /*
    #styleguide
    インラインブロック化

    インラインブロック化し、指定のピクセル値分の幅に表示制御します。
    ※ 指定値は下限40〜上限140までで、10刻みで指定可能です。

    ```
    <hr class="hr u-block__inline100">
    <hr class="hr u-block__inline120">
    <hr class="hr u-block__inline140">
    <hr class="hr u-block__inline160">
    <hr class="hr u-block__inline180">
    <hr class="hr u-block__inline200">
    <hr class="hr u-block__inline220">
    <hr class="hr u-block__inline240">
    <hr class="hr u-block__inline260">
    <hr class="hr u-block__inline280">
    <hr class="hr u-block__inline300">
    <hr class="hr u-block__inline320">
    <hr class="hr u-block__inline340">
    <hr class="hr u-block__inline360">
    <hr class="hr u-block__inline380">
    <hr class="hr u-block__inline400">
    <hr class="hr u-block__inline420">
    <hr class="hr u-block__inline440">
    <hr class="hr u-block__inline460">
    <hr class="hr u-block__inline480">
    <hr class="hr u-block__inline500">
    ```
    */
  $minimum-size: 100;
  $size-scale: 20;
  // input のデフォルトクラスのみ、type 属性セレクタも normalize で含んでしまっているため、詳細度で打ち勝つために複合セレクタとする
  @for $i from 0 through 20 {
    &__inline#{$i * $size-scale + $minimum-size},
    &__inline#{$i * $size-scale + $minimum-size}.input {
      width: #{$i * $size-scale + $minimum-size}px;
      display: inline-block; } }

  &__inline,
  &__inline.input {
    display: inline-block; }

  &__auto {
    width: auto; }

  &__lead {
    margin: 30px auto 40px;

    &:first-child {
      margin-top: 0; }

    &:last-child {
      margin-bottom: 0; } }

  &__center {
    margin-left: auto;
    margin-right: auto; }

  &__left {
    margin-left: 0;
    margin-right: auto; }

  &__right {
    margin-left: auto;
    margin-right: 0; } }
