&__page {
  $root: &;

  #{$root}--item {
    font-weight: normal;
    font-size: $font-x-large; }

  &.is-about {
    width: #{$global-contents-width-pc}px;
    margin: 0 auto;
    padding-bottom: 100px;

    #{$root}--item {
      display: inline-block;
      position: relative;
      line-height: 1;
      color: rgba($font-white, 1);
      font-family: serif;
      text-shadow: 0 0 1em rgba($font-black, 1);
      padding-bottom: #{$global-section-vertical-prop-pc}px;

      &::before {
        content: "";
        width: 80px;
        height: 1px;
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        background-color: rgba($bg-white, 1); } }

    #{$root}--label {
      line-height: 1;

      &.is-primary {
        font-size: $font-xx-huge; }

      &.is-secondary {
        font-size: $font-xx-large;
        font-weight: bold;
        margin-left: 1.5em; } }

    @include breakpoint($max-media: tablet) {
      width: 100%;
      padding-bottom: 60px;

      #{$root}--item {
        display: block;
        text-align: center;
        padding-bottom: #{$global-section-vertical-prop-sp}px; }

      #{$root}--label {
        &.is-primary {
          font-size: $font-x-huge; }

        &.is-secondary {
          font-size: $font-x-large; } } }

    @include breakpoint($max-media: sp) {
      padding-bottom: 40px;

      #{$root}--label {
        display: block;

        &.is-primary {
          font-size: $font-x-huge; }

        &.is-secondary {
          font-size: $font-normal;
          margin: {
            top: 0.8em;
            left: 0; } } } } }

  &.is-post {
    $hero-height: 480;
    $article-bump: 160;
    height: #{$hero-height}px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: -#{$header-height-pc}px auto 0;
    padding: #{$header-height-pc}px 0 #{$article-bump}px;
    position: relative;
    background: {
      size: cover;
      position: center center; }
    z-index: 1;

    #{$root}--item {
      width: 100%;
      max-width: #{$global-contents-width-pc}px;
      display: flex;
      align-items: center;
      position: absolute;
      bottom: #{$article-bump - 20}px;
      left: 50%;
      transform: translateX(-50%);
      padding-left: #{$global-section-horizontal-prop-pc}px; }

    #{$root}--label {
      display: block;
      line-height: 1;
      color: rgba($font-white, 1);

      &.is-primary {
        font-size: $font-xx-huge; }

      &.is-secondary {
        font-size: $font-xx-large;
        font-family: serif;
        font-weight: bold;
        margin-left: 30px;
        filter: drop-shadow(2px 2px 2px rgba($bg-black, 0.5)); } }

    @include breakpoint {
      $hero-height: 320;
      $article-bump: 80;
      height: #{$hero-height}px;
      margin: -#{$header-height-sp}px auto 0;
      padding: #{$header-height-sp}px 0 #{$article-bump}px;

      #{$root}--item {
        bottom: #{$article-bump - 12}px; }

      #{$root}--label {
        &.is-primary {
          font-size: $font-x-huge; }

        &.is-secondary {
          font-size: $font-x-large; } } }

    @include breakpoint($max-media: sp) {
      #{$root}--item {
        bottom: #{$article-bump - 6}px; }

      #{$root}--label {
        &.is-primary {
          font-size: $font-huge; }

        &.is-secondary {
          font-size: $font-normal; } } } } }
