&__toggle {
  $root: &;

  &.is-footer {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    line-height: 1;

    $button-size: 2em;
    #{$root}--item {
      width: $button-size;
      height: $button-size;
      overflow: hidden;
      position: relative;
      line-height: 1;
      color: rgba($font-white, 0);
      border: 1px solid $border-gray;
      border-radius: $border-radius;

      $line-length: ($button-size * 0.4);
      $line-weight: 2px;
      &::before,
      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba($bg-white, 1); }

      &::before {
        width: $line-length;
        height: $line-weight;
        transition: opacity 0.5s ease, transform 0.5s ease; }

      &::after {
        width: $line-weight;
        height: $line-length;
        transition: transform 0.5s ease; } }

    &.is-open {
      #{$root}--item {
        &::before,
        &::after {
          transform: translate(-50%, -50%) rotate(270deg); }

        &::before {
          opacity: 0; } } } }

  @include breakpoint($max: false, $min: true, $min-media: phablet) {
    display: none; } }
