&__drawerNavigation {
  display: flex;
  flex-direction: column;
  padding: 120px 0;

  & &--description {
    flex-grow: 1;
    margin-top: 120px;
    padding: {
      left: #{$global-margin-pc}px;
      right: #{$global-margin-pc}px; } }

  & &--action {
    &.is-header {
      $close-button-size: 24px;
      width: $close-button-size;
      height: #{$header-height-pc}px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: absolute;
      top: 0;
      right: 24px; } }

  @include breakpoint {
    padding: 60px 0;

    & &--description {
      margin-top: 60px;
      padding: {
        left: #{$global-margin-sp}px;
        right: #{$global-margin-sp}px; } }

    & &--action {
      &.is-header {
        $close-button-size: 24px;
        width: $close-button-size;
        height: auto;
        display: block;
        position: absolute;
        top: 20px;
        right: 20px; } } } }
