.f-header {
  $root: &;
  width: 100%;
  position: fixed;
  // position: -webkit-sticky
  // position: sticky
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  background-color: rgba($bg-black, 0);
  transition: background-color 0.5s ease;
  z-index: 10;

  &.is-fill {
    background-color: rgba($bg-black, 1); } }

