&__calendar {
  $root: &;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  &.is-saturday {
    #{$root}--item {
      &.is-date {
        color: $font-light-blue; } } }

  &.is-holiday,
  &.is-sunday {
    #{$root}--item {
      &.is-date {
        color: $font-light-red; } } }

  &.is-today {
    #{$root}--item {
      &.is-date,
      &.is-event {
        color: $font-yellow; } } }

  & &--item {
    display: block;
    text-align: center;

    &.is-date {
      font: {
        size: $font-x-large;
        weight: bold;
        family: Arial, sans-serif; } }

    &.is-event {
      font-size: $font-xx-small;
      margin-top: 0.5em; } }

  @include breakpoint($max-media: sp) {
    #{$root}--item {
      &.is-date {
        font-size: $font-normal; } } } }

