.u-link {
  &__blank {
    &::after {
      $icon-size: 1em;
      content: '';
      width: $icon-size;
      height: $icon-size;
      display: inline-block;
      position: relative;
      top: 0.1em;
      margin-left: 0.2em;
      background: url(/assets/images/window_01.svg) no-repeat center center;
      background-size: cover; } }

  &__forward {
    &::after {
      @include iconTriangle(right, $bg-x-dark-gray, 8px, 4px);
      margin: -0.3em 0 0 0.3em; } }

  &__backward {
    &::before {
      @include iconTriangle(left, $bg-x-dark-gray, 8px, 4px);
      margin: -0.3em 0.3em 0 0; } }

  &__emphasis {
    color: $font-red; }

  &__block {
    display: block; }

  &__black {
    color: $font-black;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  &__white {
    color: $font-white;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  &__tooltip {
    position: relative;

    &::before,
    &::after {
      display: block;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      transition: opacity 0.5s ease, bottom 0.3s ease; }

    &::before {
      $triangle-width: 12px;
      $triangle-height: 8px;
      content: '';
      width: 0;
      height: 0;
      bottom: -4px;
      border: {
        style: solid;
        width: 0 ($triangle-width / 2) $triangle-height ($triangle-width / 2);
        color: transparent transparent $bg-x-dark-gray transparent; } }

    &::after {
      content: attr(data-tooltip);
      bottom: -26px;
      color: $font-white;
      font-size: $font-xx-small;
      padding: 0.4em 1em;
      background-color: rgba($bg-x-dark-gray, 1);
      border-radius: $border-radius;
      white-space: nowrap; }

    &:hover {
      &::before,
      &::after {
        opacity: 1; }

      &::before {
        bottom: -14px; }

      &::after {
        bottom: -36px; } } } }
