&__globalNavigation {
  $root: &;

  &.is-drawer {
    text-align: center;

    #{$root}--item {
      color: rgba($font-white, 1);
      font-size: $font-x-large;
      font-weight: bold;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } }

    @include breakpoint {
      #{$root}--item {
        font-size: $font-large; } } }

  &.is-header {
    height: 100%;
    display: flex;
    align-items: center;

    #{$root}--item {
      color: rgba($font-white, 1);
      font-size: $font-small;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } } }

  &.is-footer {
    #{$root}--item {
      display: block;
      color: $font-white;
      font-size: $font-small;
      text-align: center;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } }

    @include breakpoint {
      #{$root}--item {
        display: inline; } } } }
