/*
  #overview
  テキストユーティリティ

  テキスト系のユーティリティモジュールです。
  ユーティリティのため、例外的に Element を持たずに Block 単体で生きることが可能です。
  他の Block にマルチクラスのようにしてスタイルを乗せる形で使用します。
  */
.u-text {
  /*
    #styleguide
    左寄せテキストユーティリティ
    ```
    <p class="p u-text__left">サンプルテキスト。サンプルテキスト。</p>
    ```
    */
  &__left {
    text-align: left; }

  /*
    #styleguide
    右寄せテキストユーティリティ
    ```
    <p class="p u-text__right">サンプルテキスト。サンプルテキスト。</p>
    ```
    */
  &__right {
    text-align: right; }

  /*
    #styleguide
    中央寄せテキストユーティリティ
    ```
    <p class="p u-text__center">サンプルテキスト。サンプルテキスト。</p>
    ```
    */
  &__center {
    text-align: center; }

  /*
    #styleguide
    文中注釈ユーティリティ

    ```
    <sup class="sup u-text__marker">※</sup>

    <p class="p">サンプルテキスト<sup class="sup u-text__marker">※</sup>。サンプルテキスト。</p>
    ```
    */
  &__marker {
    font-size: $font-x-small;
    margin: 0 0.2em; }

  /*
    #styleguide
    必須項目ユーティリティ

    ```
    <dt class="dt u-text__required">郵便番号</dt>
    ```
    */
  &__required {
    &::after {
      content: '*';
      color: $font-navy;
      margin-left: 0.2em; } }

  /*
    #styleguide
    小字（font-size: 1.2rem）ユーティリティ

    ```
    <dl class="dl">
      <dt class="dt u-text__small">Lorem ipsum</dt>
      <dd class="dd">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</dd>
    </dl>
    ```
    */
  &__small {
    font-size: 86%; }

  /*
    #styleguide
    太字（font-weight: bold）ユーティリティ

    ※ 構造上の意味を持った太字の場合は、em 要素を使用するか、オブジェクトモジュールとして切り出して下さい。

    ```
    <p class="p u-text__bold">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
    ```
    */
  &__bold {
    font-weight: bold; }

  /*
    #styleguide
    細字（font-weight: normal）ユーティリティ

    ```
    <em class="em u-text__weak">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</em>
    ```
    */
  &__weak {
    font-weight: normal; }

  /*
    #styleguide
    要素の先頭に data 属性で指定された文字列を付与するユーティリティ

    ```
    <p class="p u-text__before" data-before="■">Lorem ipsum dolor sit amet, soleat scripta scriptorem cu his.</p>
    ```
    */
  &__before {
    &::before {
      content: attr(data-before); } }

  /*
    #styleguide
    要素の末尾に data 属性で指定された文字列を付与するユーティリティ

    ```
    <p class="p u-text__after" data-after="：">Lorem ipsum dolor sit amet, soleat scripta scriptorem cu his.</p>
    ```
    */
  &__after {
    &::after {
      content: attr(data-after); } }

  /*
    #styleguide
    テキストを部分的にインラインブロック化するユーティリティ

    ※ 単語内や一定の文節内での改行を発生させたくない場合の例外措置のためのもので、基本的にはブラウザのレンダリング機能にまかせてください。

    ```
    <p class="p"><span class="span u-text__block">ご購入やお得な情報を受け取るには</span><span class="span u-text__block">会員登録が必要です</span></p>
    ```
    */
  &__block {
    display: inline-block; }

  /*
    #styleguide
    キャンセル表示

    ```
    <p class="p u-text__canceled">0000004201</p>
    ```
    */
  &__canceled {
    color: $font-gray;
    text-decoration: line-through; }

  &__error {
    color: $font-error;
    font-weight: bold; }

  &__leftGap {
    margin-left: 1em; }

  &__rightGap {
    margin-right: 1em; }

  /*
    #styleguide
    改行禁止

    ```
    <p class="p u-text__nowrap">改行されません。改行されません。</p>
    ```
    */
  &__nowrap {
    white-space: nowrap; }

  /**
   * 強制改行
   *
   * ```
   * <p class="p u-text__break">http://longlonglonglonglonglongword.com/</p>
   * ```
   */
  &__break {
    word-break: break-all; }

  /*
    #styleguide
    改行をブラウザ表示に反映するユーティリティ

    ```
    <p class="p u-text__preWrap">サンプルテキスト。
    サンプルテキスト。</p>
    ```
    */
  &__preWrap {
    white-space: pre-wrap; } }
