&__viewMore {
  $root: &;
  $height: 40px;
  $bg-color: $bg-xx-dark-navy;
  width: 6.5em;
  position: relative;
  margin: #{$global-margin-pc}px auto 0;

  & &--item {
    position: relative;
    display: block;
    line-height: $height;
    color: rgba($font-white, 1);
    text-align: center;
    text-decoration: none;
    background-color: rgba($bg-color, 1);
    transition: background-color 0.5s ease;

    &::before,
    &::after {
      content: "";
      width: ($height / 2);
      height: 100%;
      position: absolute;
      top: 0;
      display: block;
      background-color: rgba($bg-color, 1);
      transition: background-color 0.5s ease; }

    &::before {
      left: -($height / 2);
      border-radius: 100% 0 0 100%/50% 0 0 50%; }

    &::after {
      right: -($height / 2);
      border-radius: 0 100% 100% 0/0 50% 50% 0; }

    &:hover {
      background-color: rgba($bg-color, 0.5);

      &::before,
      &::after {
        background-color: rgba($bg-color, 0.5); } } }

  @include breakpoint {
    margin-top: #{$global-margin-sp}px; } }
