&__youtube {
  $root: &;
  max-width: 480px;
  position: relative;
  margin: 0 auto;
  border: 8px solid $border-dark-gray;

  &::before,
  &::after {
    transition: all 0.2s ease-in; }

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba($bg-black, 0);
    z-index: 1; }

  &::after {
    width: 100%;
    content: "近日公開予定！";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    line-height: 100%;
    font: {
      size: $font-huge;
      family: sans-serif;
      weight: bold; }
    text-align: center;
    transform: translate(-50%, -50%);
    opacity: 0;
    z-index: 1; }

  &:hover {
    cursor: pointer;

    &::before {
      background-color: rgba($bg-black, 0.7); }

    &::after {
      opacity: 1; } }

  & &--item {
    width: 100%;
    display: block;

    $play-button-size: 48px;
    &::before {
      @include iconTriangle($direction: right, $color:$bg-white, $width: $play-button-size, $height: ($play-button-size / 1.5));
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-35%, -50%); }

    $space: 20px;
    &::after {
      content: "";
      width: $play-button-size + $space;
      height: $play-button-size + $space;
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 4px solid $border-white;
      border-radius: 50%; } }

  & &--image {
    width: 100%;
    height: auto;
    display: block; } }
