@mixin offsetGlobalGutter {
  margin: {
    left: -#{$global-section-horizontal-prop-pc}px;
    right: -#{$global-section-horizontal-prop-pc}px; } }

@mixin offsetSectionGutter {
  > .section {
    @include offsetGlobalGutter; }

  + .section {
    margin-top: 0; }

  + .section,
  + .form > .section {
    margin-top: -#{$global-section-vertical-prop-pc}px;
    border-top: none; }

  + .f-section {
    margin-top: -#{$global-section-vertical-prop-pc}px; } }

@mixin offsetRowsGutter {
  > .l-rows.is-border {
    @include offsetGlobalGutter; }

  + .l-rows.is-border {
    margin-top: -#{$global-section-vertical-prop-pc}px; } }

/**
 * main
 */
.main {
  > .article:first-child {
    > .f-section__hero:first-child {
      margin-top: -#{$header-height-pc}px;

      @include breakpoint {
        margin-top: -#{$header-height-sp}px; } } } }

/**
 * section
 */
.section {
  @include offsetSectionGutter;
  @include offsetRowsGutter;

  > .section {
    &:first-child {
      margin-top: -#{$global-section-vertical-prop-pc}px; }

    &:last-child {
      margin-bottom: -#{$global-section-vertical-prop-pc}px; } }

  > .h1 + .section {
    border-top: none; } }

.section > .form {
  @include offsetSectionGutter;
  @include offsetRowsGutter;

  + .section {
    margin-top: 0; }

  &:first-child {
    > .section:first-child {
      margin-top: -#{$global-section-vertical-prop-pc}px; } }

  &:last-child {
    > .section:last-child {
      margin-bottom: -#{$global-section-vertical-prop-pc}px; } } }

/**
 * unit
 */
.l-unit__post--link {
  > .l-unit__post--image {
    > .o-figure__post {
      transform: scale(1);
      transition: transform 0.5s ease; } }

  &:hover {
    > .l-unit__post--image {
      > .o-figure__post {
        transform: scale(1.3); } } } }

/**
 * heading
 */
.h1,
.o-heading__page {
  + .section {
    border-top: none; }

  + .f-section {
    padding-top: 0; } }
