/*
  #overview
  ロゴ画像
  */
.o-logo {
  &__nankyoku {
    $root: &;
    margin: 0 auto;

    & &--item {
      width: 100%;
      text-align: center; }

    & &--link,
    & &--image {
      display: block; }

    &.is-header {
      $image-width: 60px;
      width: $image-width;

      @include breakpoint {
        $image-width: 60px;
        width: $image-width; } }

    &.is-footer {
      $image-width: 120px;
      width: $image-width;
      margin: 40px auto 20px;

      &:first-child {
        margin-top: 0; }

      &:last-child {
        margin-bottom: 0; }

      @include breakpoint {
        $image-width: 80px;
        width: $image-width; } }

    &.is-drawerNavigation {
      $image-width: 180px;
      width: $image-width;

      @include breakpoint {
        $image-width: 120px;
        width: $image-width; } }

    &.is-home {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 3;

      $width: 190;
      $margin: 80;
      #{$root}--item {
        max-width: #{$width}px;
        margin: #{($header-height-pc + $margin)}px auto 0; }

      #{$root}--image {
        display: block;
        position: relative;
        z-index: 1; }

      @include breakpoint($max-media: sp) {
        $margin: 60;
        #{$root}--item {
          width: getRelativeValue($width, $global-contents-width-sp);
          max-width: 100%;
          margin-top: #{($header-height-sp + $margin)}px; } } } } }


