&__carouselThumbnail {
  $root: &;
  margin: 30px 0;
  padding-bottom: 10px;
  border-bottom: 2px solid rgba($border-white, 0);

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  &.is-active {
    border-bottom: 2px solid $border-gold; }

  & &--item {
    $image-width: 530;
    $image-height: 310;
    display: block;
    position: relative;

    &::before {
      width: 0;
      height: 0;
      display: block;
      padding-top: imageAspectVertical($image-width, $image-height); } }

  & &--image {
    display: block;

    &[data-is-liquid="true"] {
      width: 100%;
      height: auto;
      vertical-align: bottom; } }

  & &--caption {
    display: block;
    font-size: $font-small;
    margin-top: 4px;

    &:first-child {
      margin-top: 0; } } }
