/*
  #overview
  行レイアウト
  */
.l-rows {
  $root: &;
  margin-top: 20px;

  &:first-child {
    margin-top: 0; }

  & > &--row {
    margin-top: 40px;

    &:first-child {
      margin-top: 0; } }

  &.is-border {
    > #{$root}--row {
      margin-top: 0;
      padding: #{$global-section-vertical-prop-pc}px #{$global-section-horizontal-prop-pc}px;
      border-top: 1px solid $border-gray;

      &:first-child {
        padding-top: 0;
        border-top: none; }

      &:last-child {
        padding-bottom: 0; } } }

  &.is-box {
    border: 1px solid $border-gray;

    > #{$root}--row {
      margin-top: 0;
      padding: 10px;
      border-top: 1px solid $border-gray;

      &:first-child {
        border-top: none; } } }

  @import "_partials/_share.sass"; }
