&__information {
  $root: &;
  display: table;
  margin: 40px auto 0;

  & &--unit {
    display: table-row;
    margin-top: 10px; }

  & &--term,
  & &--description {
    display: table-cell;
    padding: 30px 0; }

  & &--term {
    width: 140px;
    text-align: center;
    border-right: 1px solid $border-gray; }

  & &--description {
    &.is-category {
      width: 130px;
      padding-left: 30px; } }

  @include breakpoint {
    display: block;
    margin-top: 30px;

    $margin-size: 20;
    $prop-size: 10;
    & &--unit {
      display: flex;
      flex-wrap: wrap;
      margin-top: #{$margin-size}px;
      padding: 0 #{$prop-size}px #{$margin-size}px;
      border-bottom: 1px solid $border-gray;

      &:first-child {
        margin-top: 0; } }

    & &--term,
    & &--description {
      display: block;
      padding: 0; }

    & &--term {
      width: 100%;
      text-align: left;
      margin-bottom: 10px;
      border-right: none; }

    $text-area-width: ($global-contents-width-phablet - $prop-size * 2);
    $category-width: 140;
    & &--description {
      width: getRelativeValue(($text-area-width - ($category-width + $margin-size)), $text-area-width);

      &.is-category {
        width: getRelativeValue($category-width, $text-area-width);
        margin-right: getRelativeValue($margin-size, $text-area-width);
        padding-left: 0; } } } }

