&__section {
  $root: &;

  margin: 50px 0 30px;

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  & &--item {
    font-size: $font-x-large;
    font-weight: normal; }

  &.is-bold {
    #{$root}--item {
      font-weight: bold; } }

  &.is-home {
    #{$root}--item {
      display: flex;
      flex-direction: column;
      align-items: center;
      font: {
        weight: 800;
        family: 'M PLUS 1p', sans-serif; }
      text-align: center; }

    #{$root}--label {
      &.is-primary {
        font: {
          size: $font-huge; } }

      &.is-secondary {
        position: relative;
        font: {
          size: $font-x-large;
          weight: 500; }
        order: -1; }

      @include breakpoint($max-media: tablet) {
        &.is-primary {
          font-size: $font-xx-large; }

        &.is-secondary {
          font: {
            size: $font-normal;
            weight: 300; } } } }

    &.is-youtube,
    &.is-promotion {
      #{$root}--item {
        color: $font-black; } }

    &.is-youtube {
      #{$root}--label {
        $icon-width: 18px;
        $icon-margin: 6px;
        padding-left: $icon-width + $icon-margin;

        &.is-secondary {
          &::before {
            content: "";
            width: $icon-width;
            height: 12px;
            display: block;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            background: {
              image: url(/assets/images/youtube_black.svg);
              repeat: no-repeat;
              size: contain; } } } } }

    @include breakpoint {
      font-size: $font-xx-large; } }

  &.is-about {
    #{$root}--item {
      font-size: $font-xx-large; }

    @include breakpoint($max-media: pc) {
      #{$root}--item {
        font-size: $font-x-large; } } }

  &.is-tools {
    #{$root}--label {
      display: block;
      text-align: center;

      &.is-primary {
        font-size: $font-x-large; }

      &.is-secondary {
        font-size: $font-huge; } }

    @include breakpoint($max-media: tablet) {
      #{$root}--label {
        &.is-primary {
          font-size: $font-normal; }

        &.is-secondary {
          font-size: $font-xx-large; } } }

    @include breakpoint($max-media: sp) {
      #{$root}--label {
        &.is-primary {
          font-size: $font-x-small; }

        &.is-secondary {
          font-size: $font-x-large; } } } } }

