&__carouselController {
  $root: &;
  $padding: 10;

  width: 100%;
  height: 100%;

  & &--item {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
    overflow: hidden;
    color: rgba($font-white, 0);
    border: none;

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 50%; } }

  &.is-prev {
    #{$root}--item {
      border-radius: 0 6px 6px 0;

      &::before {
        @include iconArrow(left, $border-xx-dark-gray, 24px);
        // [注] スタイル記載位置
        // mixin で include している iconArrow で transform を使用しており、
        // アイコンの位置調整に transform を使うには、mixin の include のあとに定義してカスケードする必要がある。
        // prev / next ともに同じ値だが、& &--item に定義すると、iconArrow の mixin のスタイル詳細度に勝てないため、冗長だが個別定義とする
        transform: scaleX(0.8) rotate(-45deg) translate(-50%, -50%); } } }

  &.is-next {
    #{$root}--item {
      border-radius: 6px 0 0 6px;

      &::after {
        @include iconArrow(right, $border-xx-dark-gray, 24px);
        transform: scaleX(0.8) rotate(-45deg) translate(-50%, -50%); } } } }
