* {
  box-sizing: border-box; }

html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 10px;

  &[data-is-screen-lock="true"] {
    touch-action: none;
 } }    // PC は overflow: hidden 入れるとスクロールバーの表示・非表示で画面がガタつくのでやむを得ずスクロール許容

body {
  width: 100%;
  min-height: 100%;
  line-height: 1.8;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: 100%; }

header, main, footer,
article, aside, section,
nav,
div,
table,
h1, h2, h3, h4, h5, h6,
ul, ol, dl,
p,
input, button, textarea, select,
hr {
  font-size: inherit;
  margin: 0;
  padding: 0; }

table,
h1, h2, h3, h4, h5, h6,
ul, ol, dl,
p,
input, button, textarea, select,
hr {
  line-height: inherit;
  font-size: inherit;
  margin: 0;
  padding: 0; }

li, dt, dd,
caption, thead, tbody, tr, th, td {
  line-height: inherit;
  font-size: inherit;
  margin: 0;
  padding: 0; }

// Correct `block` display not defined for any HTML5 element in IE 8/9
// Correct `block` display not defined for `details` or `summary` in IE 10/11 and Firefox
// Correct `block` display not defined for `main` in IE 11
header, main, footer,
article, aside, section,
details,
figcaption, figure,
menu, nav,
summary {
  display: block; }

data, time {
  display: inline; }

ul, ol {
  list-style-type: none;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  -webkit-padding-start: 0; }

a {
  background-color: transparent;         // Remove the gray background on active links in IE 10
  -webkit-text-decoration-skip: objects; // Remove gaps in links underline in iOS 8+ and Safari 8+

  &:hover {
    text-decoration: none; }

  // Remove the outline when hovering in all browsers
  &:active, &:hover {
    outline-width: 0; } }

em, address, dfn {
  font-style: normal; }

// Correct style set to `bold` in Edge 12+, Safari 6.2+, and Chrome 18+
b, strong {
  font-weight: bolder; }

img {
  vertical-align: bottom;
  border-style: none;

  &[data-is-liquid="true"] {
    width: 100%;
    height: auto; }

  &[data-copyblock="true"] {
    pointer-events: none; } }

small {
  font-size: inherit; }

input, button, select, textarea {
  appearance: none;
  background: none;
  border-radius: 0;
  outline: none; }

a, button {
  cursor: pointer; }

button {
  border: none; }

button:disabled {
  cursor: default; }

// Firefox 40+, Internet Explorer 11-
button, select {
  text-transform: none; }

// Internet Explorer 11+
select {
  &::-ms-expand {
    display: none; }

  &::-ms-value {
    color: currentColor; } }

// Internet Explorer 11+
// Specify textarea resizability
textarea {
  overflow: auto;
  resize: vertical; }

// Address `overflow` set to `hidden` in IE 8/9/10/11
button {
  overflow: visible; }

// Remove inner padding and border in Firefox 4+
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: 0; }

// Remove spin button in Chrome, Safari
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

// Remove spin button in Firefox, IE
input[type="number"] {
  -moz-appearance: textfield; }

// Show the overflow in Edge and IE
hr {
  overflow: visible; }

label {
  cursor: pointer; }

[v-cloak] {
  display: none; }

.body {
  // min-width: #{$global-width-pc}px
  color: $font-white;
  font: {
    size: $font-normal;
    family: YuMincho, 'Yu Mincho', serif; }
  word-break: break-all;
  background-color: rgba($bg-xx-dark-navy, 1);

  @include breakpoint($max-media: tablet) {
    font-size: $font-small; } }

.main {
  flex: 1 0 auto;
  position: relative;
  padding-top: #{$header-height-pc}px;

  @include breakpoint {
    padding-top: #{$header-height-sp}px; } }

.article {
  position: relative;
  padding: 0; }

.aside {
  position: relative;
  padding: 0; }

.footer {
  position: relative;
  flex: 0 0 auto;
  background-color: rgba($bg-xx-dark-navy, 1); }

.section {
  max-width: #{$global-contents-width-pc}px;
  margin: #{$global-margin-pc}px auto;
  padding: #{$global-section-vertical-prop-pc}px #{$global-section-horizontal-prop-pc}px;
  background-color: rgba($bg-white, 1);

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; } }

.div {
  margin-top: 20px;

  &:first-child {
    margin-top: 0; } }

@for $i from 1 through 6 {
  .h#{$i} {
    font-weight: normal;

    &:first-child {
      margin-top: 0; }

    &:last-child {
      margin-bottom: 0; } } }

.h1 {
  font-size: $font-x-huge;
  margin: 24px 0; }

.h2 {
  font-size: $font-huge;
  margin: 64px 0 14px; }

.h3 {
  font-size: $font-x-large;
  margin: 20px 0 14px; }

.h4 {
  margin: 20px 0 8px; }

.h5 {
  margin: 14px 0 8px; }

.h6 {
  margin: 14px 0 8px; }

.a {
  color: $font-navy;
  text-decoration: underline;

  &:hover {
    text-decoration: underline; } }

.p {
  margin: 12px 0 0;

  &:first-child {
    margin-top: 0; } }

.small {
  font-size: 80%; }

.em {
  font-weight: bold; }

.strong {
  color: $font-red;
  font-weight: bold; }

.img {
  vertical-align: bottom; }

.ul, .ol {
  margin-top: 12px;

  &:first-child {
    margin-top: 0; }

  .li {
    margin-top: 6px;

    &:first-child {
      margin-top: 0; } } }

.dl {
  margin-top: 20px;

  &:first-child {
    margin-top: 0; } }

.dt {
  margin-top: 14px;

  &:first-child {
    margin-top: 0; } }

.dd {
  margin-top: 10px;

  &:first-child {
    margin-top: 0; } }

.table {
  width: 100%;
  margin-top: 12px;
  border-collapse: collapse;

  &:first-child {
    margin-top: 0; } }

.caption {
  font-size: $font-large;
  font-weight: bold;
  text-align: left;
  margin-bottom: 8px; }

.colgroup,
.col {
  @for $i from 1 through 19 {
    // \0025 = 「%」
    // is-5% 〜 is-95% (5% 刻み)
    &.is-#{$i * 5}\0025 {
      width: $i * 5%; } } }

.th,
.td {
  text-align: left;
  vertical-align: top;
  padding: 10px 4px; }

.tbody .th,
.tbody .td {
  border-bottom: 1px solid $border-light-gray; }

.th {
  font-weight: bold; }

.thead .th {
  color: $font-navy;
  font-weight: bold;
  text-align: center;
  padding: 10px;
  border-bottom: 1px solid $border-x-dark-gray; }

.input[type="text"],
.input[type="password"],
.input[type="number"],
.textarea {
  width: 100%;
  line-height: 1.5;
  display: block;
  padding: 7px 10px 8px;
  background-color: rgba($bg-white, 1);
  border: 1px solid $border-gray;
  border-radius: $border-radius;

  &:focus {
    padding: 6px 9px 7px;
    border: 2px solid $border-gold; } }

.textarea {
  min-height: 8em; }

.input[type="radio"] {
  width: 1em;
  height: 1em;
  display: inline-block;
  vertical-align: middle;
  margin: -2px 4px 0 0;
  background-color: rgba($bg-white, 1);
  border: 1px solid $border-gray;
  border-radius: 50%;
  cursor: pointer;

  &:checked {
    position: relative;

    &::after {
      content: '';
      width: 0.5em;
      height: 0.5em;
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-0.25em, -0.25em);
      background-color: rgba($bg-black, 1);
      border-radius: 50%; } } }

.input[type="checkbox"] {
  @include checkboxStyle;
  cursor: pointer;

  &:checked {
    background-color: rgba($bg-black, 1); } }

.button {
  display: inline;
  color: $font-black;
  text-decoration: underline;

  &:hover {
    text-decoration: none; } }

.hr {
  margin-top: 36px;
  border: none;
  border-top: 1px solid $border-gray; }

*[data-wmonly="true"] {
  height: 0;
  overflow: hidden;
  display: none; }
