.f-container {
  height: 100%;
  min-height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  margin: 0 auto;

  &::before {
    content: '';
    transition: background-color 0.3s; }

  &[data-is-screen-lock="true"] {
    &::before {
      width: 100%;
      height: 100%;
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 20; }

    &.is-blackOut::before {
      background-color: rgba($bg-black, 0.7); } }

  & &--body {
    height: 100%;
    display: flex;
    flex-direction: column; } }
