&__calendar {
  $root: &;
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  margin: 20px auto 0;
  padding-bottom: 40px;
  background-color: rgba($bg-xx-dark-gray, 1);
  border-radius: $border-radius;
  box-shadow: 0 0 20px rgba($bg-black, 0.7);
  // background: linear-gradient(rgba($bg-xx-dark-navy, 0.3), rgba($bg-x-light-navy, 0.2))

  &:first-child {
    margin-top: 0; }

  & &--header {
    padding: 1.8em 0 0; }

  & &--action {
    order: 1;
    margin-top: 20px;

    &:first-child {
      margin-top: 0; } }

  & &--footer {
    margin-top: 16px;

    &:first-child {
      margin-top: 0; } }

  &.is-modal {
    padding: 36px 20px 20px; } }
