&__general {
  $root: &;
  margin-top: 14px;
  padding: 0;

  &:first-child {
    margin-top: 0; }

  $note-size: 1.4em;
  & &--item {
    position: relative;
    margin-top: 8px;
    padding-left: $note-size;
    color: $font-gray;

    &:first-child {
      margin-top: 0; }

    &::before {
      content: '※';
      width: $note-size;
      display: inline-block;
      text-align: left;
      margin-left: -#{$note-size}; } }

  &.is-wide {
    $note-size: 2.4em;
    #{$root}--item {
      padding-left: $note-size;

      &::before {
        content: '(注)';
        width: $note-size;
        margin-left: -#{$note-size}; } } } }
