&__information {
  $root: &;

  &.is-home {
    $vertical-prop: 4;
    $horizontal-prop: 20;

    @include base-layout(top, $root);
    overflow: hidden;
    position: relative;
    color: $font-black;
    padding-bottom: 2em; // カテゴリラベル分
    background-color: rgba($bg-white, 1);
    box-shadow: 0 0 10px rgba($bg-black, 0);
    cursor: default;

    #{$root}--header {
      position: absolute;
      left: 0;
      bottom: 0.2em;
      padding: {
        left: #{$horizontal-prop}px;
        right: 6em; } }

    #{$root}--description {
      margin-top: #{$vertical-prop}px;
      padding: 0 #{$horizontal-prop}px;
      transition: opacity 0.5s ease; }

    #{$root}--footer {
      &.is-category {
        width: 4.5em;
        position: absolute;
        right: 0;
        bottom: 0; } }

    @include breakpoint($max-media: sp) {
      $image-width: 160;
      $image-height: 90;
      $margin-size: 10;
      @include base-layout(left, $root, $image-width, $image-height, $margin-size);

      #{$root}--header {
        margin-left: #{($image-width + $margin-size)}px;
        padding: {
          left: 0;
          right: 5em; } }

      #{$root}--description {
        margin: 0;
        padding: 0.2em 0 0; }

      #{$root}--image {
        height: auto; } } } }


