@import 'sprite';

// 2x images for Retina
@mixin sprite-width2x($sprite) {
  width: (nth($sprite, 5) / 2); }

@mixin sprite-height2x($sprite) {
  height: (nth($sprite, 6) / 2); }

@mixin sprite-position2x($sprite) {
  $sprite-offset-x: (nth($sprite, 3) / 2);
  $sprite-offset-y: (nth($sprite, 4) / 2);
  background-position: $sprite-offset-x  $sprite-offset-y; }

@mixin sprite-size2x($sprite) {
  background-size: (nth($sprite, 7) / 2) (nth($sprite, 8) / 2); }

@mixin sprite2x($sprite) {
  @include sprite-image($sprite);
  @include sprite-position2x($sprite);
  @include sprite-width2x($sprite);
  @include sprite-height2x($sprite);
  @include sprite-size2x($sprite); }

@mixin sprites2x($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite2x($sprite); } } }

$job_post: ($job_astrologian_post: job__astrologian, $job_bard_post: job__bard, $job_blackmage_post: job__blackmage, $job_dancer_post: job__dancer, $job_darkknight_post: job__darkknight, $job_dragoon_post: job__dragoon, $job_gunbreaker_post: job__gunbreaker, $job_knight_post: job__knight, $job_machinist_post: job__machinist, $job_monk_post: job__monk, $job_ninja_post: job__ninja, $job_redmage_post: job__redmage, $job_samurai_post: job__samurai, $job_scholar_post: job__scholar, $job_summoner_post: job__summoner, $job_warrior_post: job__warrior, $job_whitemage_post: job__whitemage);
$equipment_post: main, sub, body, feet, hands, head, legs;
$follow_services: twitter, youtube;
$share_services: twitter;

@mixin iconDefaultStyle($key) {
  @include sprite2x($key);
  display: inline-block;
  vertical-align: middle; }

@mixin svgIconDefaultStyle() {
  display: inline-block;
  vertical-align: middle;
  background: {
    size: contain;
    position: center center;
    repeat: no-repeat; } }

@each $key, $value in $job_post {
  .i-#{$value}.is-post {
    @include iconDefaultStyle($key); }

  .i-#{$value}.is-home {
    @include iconDefaultStyle($key); } }

@each $parts in $equipment_post {
  .i-equipment {
    &__#{$parts} {
      &.is-post {
        $bg-color: $bg-xx-dark-navy;
        width: 32px;
        height: 32px;
        display: inline-block;
        vertical-align: middle;
        background: {
          color: rgba($bg-color, 1);
          image: url(/assets/images/equipment_#{$parts}_white.svg);
          size: contain;
          position: center center;
          repeat: no-repeat; }
        border: 8px solid $bg-color;
        border-radius: 50%;
        box-sizing: content-box; } } } }

// share SNS
@each $service in $share_services {
  .i-share {
    &__#{$service} {
      @include svgIconDefaultStyle;

      &.is-post {
        width: 26px;
        height: 26px;
        background-image: url(/assets/images/#{$service}_navy.svg);
        opacity: 0.5;
        transition: opacity 0.5s ease;

        &:hover {
          opacity: 1; } }

      &.is-global {
        width: 16px;
        height: 16px;
        background-image: url(/assets/images/#{$service}_white.svg);
        opacity: 1;
        transition: opacity 0.5s ease;

        &:hover {
          opacity: 0.5; } } } } }

@each $service in $follow_services {
  .i-follow {
    &__#{$service} {
      @include svgIconDefaultStyle;

      &.is-profile {
        width: 16px;
        height: 16px;
        background-image: url(/assets/images/#{$service}_navy.svg); }

      &.is-header {
        width: 24px;
        height: 24px;
        background-image: url(/assets/images/#{$service}_white.svg); }

      &.is-footer {
        width: 32px;
        height: 32px;
        background-image: url(/assets/images/#{$service}_black.svg); }

      &.is-drawerNavigation {
        width: 32px;
        height: 32px;
        background-image: url(/assets/images/#{$service}_white.svg); } } } }

.i-scale.is-carousel {
  @include iconDefaultStyle($scale_carousel);
  background-color: rgba($bg-black, 0.5);
  border-radius: 50%;
  transition: background-color 0.5s ease;

  &:hover {
    background-color: rgba($bg-black, 0.8); } }
