//================================
// size
//================================
$global-contents-width-pc: 1100;
$global-contents-width-tablet: 984;
$global-contents-width-phablet: 728;
$global-contents-width-sp: 500;

$global-prop-pc: 20;
$global-prop-sp: 20;
$global-width-pc: ($global-contents-width-pc + ($global-prop-pc * 2));
$global-width-tablet: ($global-contents-width-tablet + ($global-prop-pc * 2));
$global-width-phablet: ($global-contents-width-phablet + ($global-prop-pc * 2));
$global-width-sp: ($global-contents-width-sp + ($global-prop-pc * 2));

$global-margin-pc: 60;
$global-margin-sp: 40;

$global-section-vertical-prop-pc: 30;
$global-section-vertical-prop-sp: 20;
$global-section-horizontal-prop-pc: 40;
$global-section-width-pc: ($global-contents-width-pc - ($global-section-horizontal-prop-pc * 2));

$article-width: 760;
$article-contens-width: ($article-width - ($global-section-horizontal-prop-pc * 2));

$header-height-pc: 80;
$header-height-sp: 60;

$font-xx-huge: 10rem;
$font-x-huge: 6rem;
$font-huge: 4rem;
$font-xx-large: 2.6rem;
$font-x-large: 2.0rem;
$font-large: 1.8rem;
$font-normal: 1.6rem;
$font-small: 1.4rem;
$font-x-small: 1.2rem;
