/*
  #overview
  ボタン
  */
.o-action {
  $root: &;
  max-width: 380px;
  margin: 40px auto 0;
  text-align: center;

  &:first-child {
    margin-top: 0; }

  & &--item {
    width: 100%;
    position: relative;
    display: block;
    color: $font-black;
    line-height: 2.8;
    text-decoration: none;
    margin: 14px auto 0;
    padding: {
      left: 10px;
      right: 10px; }
    border: 1px solid $border-gray;
    border-radius: $border-radius;

    &:first-child {
      margin-top: 0; }

    &:hover {
      border-color: $border-dark-gray;
      text-decoration: underline; } }

  @include actionDesabled($root);

  &.is-submit {
    @include modifiedActionTrigger($root, $bg-black);
    @include actionDesabled($root); }

  &.is-transition,
  &.is-select {
    #{$root}--item {
      border: 1px solid $border-gold; }
    @include actionDesabled($root); }

  &.is-backward {
    @include actionWithArrow($root, "backward");
    @include actionDesabled($root); }

  &.is-forward {
    @include actionWithArrow($root, "forward");
    @include actionDesabled($root); }

  &.is-large {
    #{$root}--item {
      line-height: 3.6; } }

  &.is-small {
    #{$root}--item {
      font-size: $font-small;
      line-height: 3; } }  // same height as .input[type="text"]

  &.is-x-small {
    #{$root}--item {
      font-size: $font-small;
      line-height: 2.2; } }

  &.is-lite {
    #{$root}--item {
      display: inline-block;
      width: auto;
      padding: {
        left: 34px;
        right: 34px; } } }

  @import "_partials/_carouselController.sass";
  @import "_partials/_close.sass";
  @import "_partials/_follow.sass";
  @import "_partials/_globalNavigation.sass";
  @import "_partials/_hamburger.sass";
  @import "_partials/_monthController.sass";
  @import "_partials/_share.sass";
  @import "_partials/_tag.sass";
  @import "_partials/_toggle.sass";
  @import "_partials/_viewMore.sass";
  @import "_partials/_youtube.sass"; }
