&__header {
  $root: &;
  $logo-width: 180;
  $margin-size: 30;
  width: 100%;
  display: flex;
  position: relative;

  & &--col {
    &.is-logo {
      width: #{$logo-width}px; }

    &.is-globalNavigation {
      flex-grow: 1;
      margin-left: 40px; }

    &.is-hamburger {
      $hamburger-width: 24;
      width: #{$hamburger-width}px;
      height: #{$header-height-pc}px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: fixed;
      top: 0;
      right: 32px; } }

  @include breakpoint($max-media: tablet) {
    padding: 0 #{$global-prop-sp}px;

    & &--col {
      $button-width: 24;
      &.is-hamburger {
        width: #{$button-width}px;
        right: 20px; } } }

  @include breakpoint($max-media: phablet) {
    & &--col {
      &.is-logo {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }

      &.is-globalNavigation {
        display: none; }

      $button-width: 24;
      &.is-hamburger {
        width: #{$button-width}px;
        height: #{$header-height-sp}px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        top: 0;
        right: 20px; } } } }

