&__category {
  $root: &;

  &.is-information {
    padding: 0.1em;
    #{$root}--item {
      color: $font-white;
      font: {
        size: $font-x-small;
        weight: bold;
        family: sans-serif; }
      text-align: center; }

    &.is-release {
      background-color: rgba($bg-dark-blue, 1); }

    &.is-notice {
      background-color: rgba($bg-dark-red, 1); }

    &.is-event {
      background-color: rgba($bg-dark-green, 1); } } }
