/*
  #overview
  セレクター
  */
.o-selector {
  $root: &;
  width: 100%;
  max-width: 420px;
  display: block;
  margin: {
    top: 14px;
    left: 0;
    right: auto; }

  &:first-child {
    margin-top: 0; }

  & &--label {
    width: 100%;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    background-color: rgba($bg-white, 1);
    border: 1px solid $border-gray;
    border-radius: $border-radius;

    &::after {
      @include iconTriangle(down);
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%); } }

  & &--items {
    width: 100%;
    line-height: 1.5;
    padding: 7px 30px 8px 10px;
    background-color: rgba($bg-white, 1);
    border: none;
    border-radius: $border-radius; }

  &.is-date,
  &.is-month,
  &.is-year {
    #{$root}--items {
      min-width: 0; } }

  @import "_partials/_carouselIndicator.sass"; }
