&__calendar {
  $root: &;

  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;

  &:first-child {
    margin-top: 0; }

  $week-length: 7;
  & &--col {
    width: 100% / $week-length;
    min-height: 6em;
    position: relative;
    padding: 0.6em;

    &::before {
      content: "";
      width: 100%;
      height: 6px;
      display: block;
      position: absolute;
      left: 0;
      top: 0; }

    &.is-otherMonth {
      opacity: 0.3; }

    &.is-even {
      background-color: rgba($bg-x-dark-red, 1);

      &::before {
        background: linear-gradient(rgba($bg-black, 0.4), rgba($bg-black, 0)); } }

    &.is-odd {
      background-color: rgba($bg-dark-red, 1);

      &::before {
        background: linear-gradient(rgba($bg-black, 0.8), rgba($bg-black, 0)); } } } }
