&__close {
  $root: &;

  & &--item {
    display: inline;
    border: none; }

  $icon-size: 14px;
  &.is-modal,
  &.is-modal__white {
    #{$root}--item {
      padding: 18px; } }

  &.is-modal {
    #{$root}--item {
      @include closeButtonStyle($icon-size, $border-x-dark-gray);

      &:hover {
        &::before,
        &::after {
          border-color: $border-black; } } } }

  &.is-modal__white {
    #{$root}--item {
      @include closeButtonStyle($icon-size, $border-white);

      &:hover {
        &::before,
        &::after {
          border-color: $border-gray; } } } }

  &.is-drawerNavigation {
    #{$root}--item {
      @include closeButtonStyle($icon-size: 24px, $border-color: $border-white, $border-width: 2px); } } }

