&__carouselIndicator {
  $root: &;

  margin-top: 10px;

  &:first-child {
    margin-top: 0; }

  & &--items {
    display: flex;
    justify-content: center; }

  & &--item {
    margin-left: 10px;

    &:first-child {
      margin-left: 0; } }

  &.is-thumbnail {
    $cols: 5;  // サムネイルの横最大個数
    $margin-size: 20;  // サムネイル用の相対値計算用マージン基準値

    #{$root}--items {
      flex-wrap: wrap; }

    #{$root}--item {
      width: getRelativeValue(($article-contens-width - $margin-size * ($cols - 1)) / $cols, $article-contens-width);
      margin: 20px 0 0 getRelativeValue($margin-size, $article-contens-width);

      &:nth-child(#{$cols}n+1) {
        margin-left: 0; }

      &:nth-child(-n+#{$cols}) {
        margin-top: 0; } }

    @include breakpoint {
      $cols: 4;  // サムネイルの横最大個数
      #{$root}--item {
        width: getRelativeValue(($article-contens-width - $margin-size * ($cols - 1)) / $cols, $article-contens-width); } }

    @include breakpoint($max-media: sp) {
      $cols: 3;  // サムネイルの横最大個数
      #{$root}--item {
        width: getRelativeValue(($article-contens-width - $margin-size * ($cols - 1)) / $cols, $article-contens-width); } } } }
