&__ordered {
  $root: &;
  margin-top: 14px;
  counter-reset: list-order;

  $order-size: 2.0em;
  & &--item {
    margin-top: 8px;
    padding-left: $order-size;

    &:first-child {
      margin-top: 0; }

    &::before {
      width: $order-size;
      display: inline-block;
      text-align: left;
      margin-left: -#{$order-size};
      counter-increment: list-order;
      content: counter(list-order) '.'; } }

  &.is-wide {
    $order-size: 2.4em;

    #{$root}--item {
      padding-left: $order-size;

      &::before {
        width: $order-size;
        margin-left: -#{$order-size};
        content: '(' counter(list-order) ')'; } } } }
