.f-article {
  &__post {
    $root: &;
    $width: 760;
    $article-bump: 160;

    width: getRelativeValue($width, $global-contents-width-pc);
    position: relative;
    margin-top: -#{$article-bump}px;
    padding: 0;
    background-color: rgba($bg-white, 1);
    filter: drop-shadow(0 4px 5px rgba(0, 0, 0, 0.2));

    & &--body {
      padding: #{$global-section-vertical-prop-pc}px #{$global-section-horizontal-prop-pc}px; }

    & &--body {
      &:first-child {
        margin-top: 0; }

      &.is-hero {
        padding: 0; }

      &.is-equipment {
        margin-top: #{$global-section-vertical-prop-pc}px;
        border-top: 1px solid $border-light-gray; }

      &.is-job {
        background-color: rgba($bg-xx-light-gray, 1); } }

    @include breakpoint($max-media: tablet) {
      width: auto; }

    @include breakpoint {
      $article-bump: 80;

      margin-top: -#{$article-bump}px; } } }
